import styled from 'styled-components'

export const BandContainer = styled.div`
  height:20px;
  background:#010606; 
  display:flex;
  flex-direction:row;
  justify-content:center;
  align-items:center;
  font-size:0.9rem;
  position:sticky;
  top:0;
  z-index: 10;

  @media screen and ( max-width: 960px){
    transition 0.8s all ease;
  }
`

export const BandWrapper = styled.div`  
  display:flex;
  flex-direction:row;
  justify-content:right; 
  align-items:center;
  width:100%;
  padding:0 24px;

  @media screen and (max-width: 480px){
    transition 0.8s all ease;
    padding:0 12px;
  }
`
export const Banda = styled.a`
  color:#b9bbc1;
  font-weight:bold;
  text-decoration:none;
  cursor:pointer;
  transition 0.8s all ease;


  &:hover{
    color:#fff;
    transition 0.8s all ease;
  }

  @media screen and (max-width: 480px){
    transition 0.8s all ease;
    font-size:0.7rem;
  }
`
