import styled from 'styled-components';
import {AiOutlineCalendar} from 'react-icons/ai';
import {MdOutlineAdminPanelSettings} from 'react-icons/md';
import {Link as LinkR} from 'react-router-dom';
import {AiOutlineArrowLeft, AiOutlineArrowRight, AiFillCloseSquare} from 'react-icons/ai'

export const UserPanelContainer = styled.div`
  position:relative;
  height:100vh;
  width:100%;
`;

export const UserPanelWrapper = styled.div`
  position:relative;
  height:100vh;
  width:100%;
  display:grid;
  grid-template-columns:0.25fr 0.75fr;
  grid-template-rows:1fr;
  grid-template-areas:"buttons" "content";


  @media screen and (max-width:480px){
    grid-template-columns:1fr;
    grid-template-rows:60vh auto;
    grid-template-areas: "buttons" "content";
  }
`;

export const ContentButtons = styled.div`
  gird-area:buttons;
  width:100%;
  height:100%;
  padding-top:10vh;
`

export const Header = styled.div`
  font-size:3vh;
  color:var(--GreenStrong);
  width:100%;
  text-align:left;
  padding-left:5vh;
  margin-bottom:3vh;

`
export const Title = styled.div`
  font-size:2.5vh;
  color:var(--GreenStrong);
  width:100%;
  text-align:left;
  padding-left:5vh;
  margin-bottom:3vh;
`
export const WrapperSections = styled.div`
  width:100%;
  height:auto;
  display:flex;
  flex-direction:row;
  align-items:flex-start;
  justify-content:flex-start;
  flex-wrap: wrap;
  padding:2vh 0 2vh 4vw;
`
export const WrapperButtons = styled.div`
  width:100%;
  height:auto;
  display:flex;
  flex-direction:columns;
  align-items:center;
  justify-content:center;
  padding:2vh;

`
export const SectionItem = styled.div`
  height:15vh;
  width:15vh;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
  margin:0 1vw 1vh 0;
  border-radius:2.5vh;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  cursor:pointer;

  &.isActive{
    border:5px solid  var(--GreenStrong);
  }
`
export const SectionName = styled.div`
  width:100%;
  font-size:2vh;
  font-weight:bold;
  color:var(--GreenStrong);
  text-align:center;
`;
export const CalendarIcon = styled(AiOutlineCalendar)`
  font-size:10vh;
  color:var(--GreenStrong);
`;

export const AdminIcon = styled(MdOutlineAdminPanelSettings)`
  font-size:8vh;
  color:var(--GreenStrong);
`;

export const ContentData = styled.div`
  gird-area:content;
  width:100%;
  height:100%;
  background:linear-gradient(0deg,rgba(236,220,204,1),rgba(236,220,204,1));
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:flex-start;
  padding:0 2vw;
`
export const HeadersRow = styled.div`
  width:60%;
  height:15vh;
  display:flex;
  flex-direction:row;
  align-items:center;
  justify-content:space-around;
  border-bottom:1px solid var(--GreenStrong);

  @media screen and (max-width:480px){
    width:80%;
  }
`
export const HeaderContent = styled.h2`
  font-size:2vh;
  color:var(--GreenStrong);
  opacity:0.5;
  cursor:pointer;

  &.isActive{
    opacity:1;
  }


  @media screen and (max-width:480px){
    font-size:1.8vh;
  }
`;
export const DataWrapper = styled.div`
  width:100%;
  height:100%;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:flex-start;
  padding:2vh;
  overflow-y:scroll;
`
export const AppoinmentItem = styled.div`
  width:100%;
  height:auto;
  display:flex; 
  flex-direction:column;
  align-items:center;
  justify-content:center;
  padding:2vh;
  margin-bottom:2vh;
  background-color:white;
  border-radius:2.5vh;
  border:5px solid var(--GreenStrong);
`

export const IdAppoinment = styled.p`
  width:100%;
  font-size:2vh;
  color:var(--GreenStrong);
`
export const TypeOfSession = styled.p`
  width:100%;
  font-size:2vh;
  color:var(--GreenStrong);
`
export const Specialist = styled.p`
  width:100%;
  font-size:2vh;
  color:#00AAA9;
`
export const MeetingDate = styled.p`
  width:100%;
  font-size:2vh;
  color:#00AAA9;
`
export const MeetingTime = styled.p`
  width:100%;
  font-size:2vh;
  color:#00AAA9;
`
export const LinkMeetingWrapper = styled.div`
  width:100%;
  font-size:2vh;
  color:#00AAA9;
`
export const MeetingLink = styled.a`
  width:100%;
  color:#00AAA9;
  font-size:2vh;
  text-decoration:none;

  &:hover {
    text-decoration:underline;
  }
`
export const Button = styled(LinkR)`
  background: ${({primary}) => (primary ? 'var(--White)' : 'var(--White)')};
  white-space:nowrap;
  padding:${({big}) => (big ? '14px 40px' : '16px 30px')};
  color:${({dark}) => (dark ? 'var(--GreenStrong)' : 'var(--GreenStrong)')};
  font-size:${({fontBig}) => (fontBig ? '20px' : '16px')};
  outline:none;
  border:2px solid var(--GreenStrong); 
  cursor:pointer;
  display:flex;
  width:250px;
  justify-content:center;
  align-items:center;
  transition: all 0.2s ease-in-out;
  text-decoration:none;

  &:hover{
    transition: all 0.2s ease-in-out;
    background: ${({primary}) => (primary ? 'var(--GreenStrong)' : 'var(--GreenStrong)')};
    color:${({dark}) => (dark ? 'var(--White)' : 'var(--White)')};
  }
  @media screen and (max-width: 723px){
    transition: all 0.2s ease-in-out;
    width:180px;
  }
  @media screen and (max-width: 480px){
    transition: all 0.2s ease-in-out;
    width:250px;
  }
`
export const AppointmentRow = styled.div`
  width:100%;
  font-size:2vh;
  color:var(--GreenStrong);
`

export const AppointmentRowList = styled.ul`
  width:100%;
  font-size:2vh;
  color:var(--GreenStrong);
`

export const AppointmentItemList = styled.li`
  width:100%;
  font-size:2vh;
  color:var(--GreenStrong);
  list-style-position: inside;
`

export const HeaderSection = styled.div`
  width:100%;
  height:8vh;
  font-size:2vh;
  color:#00AAA9;
`;
export const SectionContent = styled.div`
  width:100%;
  height:auto;
`;
export const SectionContentWrapper = styled.div`
  height:40vh;
  border:1px solid #00AAA9;
  margin-bottom:2vh;
  background:#fff;
  display:flex;
  flex-direction:column;
  align-items:flex-start;
  justify-content:flex-start;

    width: 70vw;
    overflow-x: auto;
    white-space: nowrap;
`;

export const SectionContentAdd = styled.form`
  display:flex;  
  flex-direction:column;
  width:50%;
`;

export const ItemRow = styled.div`
  display:flex;  
  flex-direction:column;
  width:100%;
  height:auto;
  font-size:12px;
  border-bottom:2px solid #00AAA9;
  margin:1vh 0;
`
export const HeaderRow = styled.h3`
  border-bottom:2px solid #00AAA9;
`

export const HeaderSubSection = styled.h4`
  color:#00AAA9;
`
export const ArrowLeft = styled(AiOutlineArrowLeft)`
  font-size:3vh;
  color:#00AAA9;
  margin:0 2vw;
`
export const ArrowRight = styled(AiOutlineArrowRight)`
  font-size:3vh;
  color:#00AAA9;
  margin:0 2vw;
`

export const CancelRow = styled.div`
  width:100%;
  display:flex;
  align-items:center;
  justify-content:flex-end;
`
export const EdditIcon = styled(AiFillCloseSquare)`
  position:relative;
  width:10%;
  font-size:5vh;
  transition 0.8s all ease;
  &:hover {
    color: var(--GreenStrong);
    transition 0.8s all ease;
  }
  @media screen and (max-width:480px){
    font-size:4vh;
  }
`;

