import React, {useState, useEffect} from 'react';
import {useNavigate} from "react-router-dom";
import axios from 'axios';
import ImageBackground from '../../images/Background/background.png';
import NotificationBandSection from '../NotificationBand';
import {PATH_PREFIX_BACKEND} from "../../Data/EnvironmentVariables"
import {Container, FormWrap, Icon, FormContent, Form, FormH1, Formlabel, FormButton, FormInput, TextReference} from './SigninElements';

const SignIn = ({token, setToken, setUser, setLoading, user}) => {
  let navigate = useNavigate();

  const [accessoAPIlogin, setAccesoAPIlogin] = useState({tipo: 'POST', url: PATH_PREFIX_BACKEND + '/api/users/login'});
  const [accessoAPIuser, setAccesoAPIuser] = useState({tipo: 'GET', url: PATH_PREFIX_BACKEND + '/api/users/me'});

  const [errorAPI, setErrorAPI] = useState(null);
  const [respuestaAPI, setRespuestaAPI] = useState(null);

  const sendData = async (e) => {
    e.preventDefault();
    const datos = {
      email: document.getElementById('email').value,
      password: document.getElementById('password').value
    }
    const headers = {};
    const loginAPI = async () => {
      setErrorAPI(null);
      try {
        await axios({method: accessoAPIlogin.tipo, url: accessoAPIlogin.url, data: datos, headers: headers}).then(response => {
          headers['authorization'] = response.data;
          localStorage.setItem('token', JSON.stringify(response.data))
          setToken(response.data)
        })
      } catch (error) {
        if (error.response) {
          setErrorAPI(error.response.data)
        }
      }
    };
    const userAPI = async () => {
      if (typeof headers.authorization !== 'undefined') {
        setErrorAPI(null);
        try {
          const response = await axios({method: accessoAPIuser.tipo, url: accessoAPIuser.url, headers: headers})
          setRespuestaAPI(response.data)
          setUser(response.data);
          localStorage.setItem('user', JSON.stringify(response.data))
          await new Promise((r) => setTimeout(r, 4500));
          await navigate('/')
        } catch (error) {
          if (error.response) {
            setErrorAPI(error.response.data)
          }
        }
      }
    };
    await loginAPI();
    await userAPI();
  }

  /*******************************NOTIFCATION VARIABLES************************************************/
  const [typeNotification, setTypeNotification] = useState("Success");
  const [MessageNotification, setMessageNotification] = useState("No Message");
  const [TriggerNotification, setTriggerNotification] = useState(false);

  const AppearNotification = async () => {
    await new Promise((r) => setTimeout(r, 4000));
    await setTriggerNotification(false);
  };

  useEffect(() => {
    if (errorAPI !== null) {
      setTypeNotification("Error");
      setMessageNotification(errorAPI.status);
      setTriggerNotification(true);
      AppearNotification();
      setErrorAPI(null);
    } else if (respuestaAPI !== null) {
      setTypeNotification("Success");
      setMessageNotification("Ingreso Exitoso");
      setTriggerNotification(true);
      AppearNotification();
      setRespuestaAPI(null);
    }
  }, [errorAPI, respuestaAPI])

  return (
    <>
      <Container imgsrc={ImageBackground}>
        <NotificationBandSection type={typeNotification} message={MessageNotification} trigger={TriggerNotification} />
        <FormWrap onSubmit={(event) => {sendData(event)}}>
          <Icon to="/">Bambucamp</Icon>
          <FormContent>
            <Form action="#">
              <FormH1>Ingresa a tu Cuenta</FormH1>
              <Formlabel htmlFor='for'>Email</Formlabel>
              <FormInput id='email' type='email' required />
              <Formlabel htmlFor='for'>Contraseña</Formlabel>
              <FormInput id='password' type='password' required />
              <FormButton type='submit'>Ingresar</FormButton>
              <TextReference to="/recoverpassword">Olvidaste tu contraseña?</TextReference>
              <TextReference to="/register">Registrate</TextReference>
            </Form>
          </FormContent>
        </FormWrap>
      </Container>
    </>
  )
}

export default SignIn;
