import React, {useState, useEffect} from 'react';
import ImageContactUs from '../../images/ContactUs/contact.jpeg';
import {ContacUsContainer, ContactUsHeader, ContactUsSubtitle, ContactUsWrapper, Column1, Column2, Form, Label, SubmitButton, ImgContacUs, InputField, Group1, InputFieldText, Iframe} from './ContacUsElements';
import {PATH_PREFIX_BACKEND} from "../../Data/EnvironmentVariables"


const ContactUs = () => {

  return (
    <ContacUsContainer id="contactus">
      <ContactUsHeader>Contactanos</ContactUsHeader>
      <ContactUsSubtitle>Estemos en Contacto</ContactUsSubtitle>
      <ContactUsWrapper>
        <Column1>
          <Iframe name="dummyframe" id="dummyframe"></Iframe>
          <Form id="formContact" action={PATH_PREFIX_BACKEND + '/api/contact'} method="POST" target="dummyframe">
            <Group1>
              <Label>Nombre</Label>
              <InputField id="nameContactform" type="text" name="name" required="required" />
            </Group1>
            <Group1>
              <Label>Apellido</Label>
              <InputField id="surnameContactform" type="text" name="apellido" required="required" />
            </Group1>
            <Group1>
              <Label>Email</Label>
              <InputField id="emailContactform" type="email" name="email" required="required" />
            </Group1>
            <Group1>
              <Label>Mensaje</Label>
              <InputFieldText id="messageContactform" name="message" required="required" />
            </Group1>
            <Group1>
              <SubmitButton>Enviar</SubmitButton>
            </Group1>
          </Form>
        </Column1>
        <Column2 >
          <ImgContacUs imgsrc={ImageContactUs} />
        </Column2>
      </ContactUsWrapper>
    </ContacUsContainer>
  );
};
export default ContactUs;
