import styled from 'styled-components'

export const ProgressBarContainer = styled.div`
  position:absolute;
  top:50px;
  right:80px;
  min-width: 700px;
  margin: 0px auto;
  padding: 2rem 0;
  font-family: var(--Header-Family);
  color: var(--White);
  counter-reset: step-count;
`
export const ProgressBar = styled.div`
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20px, 1fr));
`;


export const StepCount = styled.div`

`;

export const StepDescription = styled.div`
  font-size: 1rem;
`;

export const StepProgres = styled.div`
  
  text-align: center;
  position: relative;

  &:before,&:after{
    background-color: var(--GreenLight);
    content: "";
    height: 4px;
    position: absolute;
    z-index: -1;
    top: 30px;
  }
  &:after{
    left:50%;
    width:100%;
  }
  
  &:last-of-type.isActive:after{
    background-color: var(--GreenLight);
  }

  &:first-of-type:before{
    @media screen and (min-width: 640px) {
      right: 50%;
      width: 50%;
  }

  &:first-of-type:before, &:last-of-type:after {
    @media screen and (max-width: 640px) {
      background-color: white !important;
      }
    }
  }
  &:last-of-type:after {
    left: 50%;
    width: 50%;
  }

  & ${StepCount}{
    background-color: var(--GreenLight);
    height: 60px;
    width: 60px;
    margin: 0px auto;
    border-radius: 50%;
    color: var(--White);
    line-height: 40px;
    z-index: 100;
    border: 7px solid var(--White);
    font-size: 1.2rem;
    font-weight: 500;
  }

  & ${StepCount}:before{
    counter-increment: step-count;
    content: counter(step-count);
  }


  &.isActive ${StepDescription}{
    font-weight:500;
  }
  
  &.isActive:after{
    background-color: #dad6ce;
  }
  
  &.isActive ~ & ${StepCount}{
    background-color: #dad6ce;
  }
  
  &.isActive ~ &:after{
    background-color: #dad6ce;
  }

`;




