import React, {useState, useEffect} from 'react';
import {CartFirstSectionContainer, CartFirstSectionWrapper, CartHeader, CartDescriptionWrapper, CarouselImage, CarosuelSkip, DescriptionGridDetail, DescriptionRow, CartDetailWrapper, SmallHeader, CartDescriptionGridPriceRow, Column1, Column2, Column3, Column4, ColP, Column1Header, Column2Header, Column3Header, HeaderP, Colh1, DescriptionRowHeader, CartRoomSection, Collabel, ButtonAddTent, ButtonAdd, LabelButton, AditionalPeopleComponent, LableAditionalPeople} from './CartFirstSection';
import {SideBottombarContainer, SideBottombarColumn1, SideBottombarColumn2, HeaderExtracolumn1P4, HeaderExtracolumn1h2, HeaderExtracolumn1P3, ListExtrasHeader, ExtraWrapperHeader, ItemListExtras, ItemListExtraPrice, ItemListExtraQty, ItemListExtraUnitPrice, ListExtraActivityHeader, ItemListExtraTitle2, ExtraHeaderColumn2, ExtraHeaderColumn1, ContainerAside} from './CartFirstSection'
import {ButtonCartForwardExtra} from '../../ButtonElements';
import {FaBed, FaUserFriends, FaPhone, FaStream} from "react-icons/fa";
import {Carousel, CarouselButtons} from "./Carousel";
import Moment from 'moment';
import {useNavigate} from "react-router-dom";
import NotificationBandSection from '../../NotificationBand';

export const CartFirstSection = ({cart, setPropCart, tentData, AvailableTents, setAvailableTents}) => {


  const [isOpen, setIsOpen] = useState(false)
  const triggerOpen = () => {
    setIsOpen(!isOpen)
  }



  let navigate = useNavigate();
  //This is logic to change buttons
  const [images, setImages] = useState({})

  const setDataImages = () => {
    for (var item of tentData) {
      var bufferImagesLength = Object.values(item.imgroutes)
      images[item.id] = {
        "id": item.id,
        "nImages": bufferImagesLength.length,
        "current": 0
      }
      setImages({...images});
    }
  }

  const nextSlide = (event) => {
    let id = event.target.id;
    var buffer = {...images};
    if (buffer[id].current < buffer[id].nImages - 1) {
      buffer[id].current = buffer[id].current + 1;
      setImages(buffer);
    } else {
      buffer[id].current = 0;
      setImages(buffer);
    }
  };

  const prevSlide = (event) => {
    let id = event.target.id;
    var buffer = {...images};
    if (buffer[id].current > 0) {
      buffer[id].current = buffer[id].current - 1;
      setImages(buffer);
    } else {
      buffer[id].current = buffer[id].nImages - 1;
      setImages(buffer);
    }
  };

  //This is logic of items iterable
  const [tents, setTents] = useState([])
  useEffect(() => {
    setDataImages();
    setTents(tentData)
  }, [tentData])

  const [SelectedTents, setSelectedTents] = useState([]);
  const [qtyAditionalAdults, setqtyAditionalAdults] = useState(0);
  const [totalImportTents, setTotalImportTents] = useState(0);


  const AditionalPeople = (event) => {
    let id = event.target.id;
    let item_id = id.substring(14);
    const CheckAditionalPeople = document.getElementById('TENT_ADPEOPLE_' + item_id);
    if (CheckAditionalPeople.checked) {
      setqtyAditionalAdults(prevQty => prevQty + 1)
      setTotalImportTents(totalImportTents + 100);
    } else {
      setqtyAditionalAdults(prevQty => prevQty - 1)
      setTotalImportTents(totalImportTents - 100);
    }
  }


  const triggerAddTent = (event) => {
    let id = event.target.id;
    let item_id = id.substring(4);
    const checkbox = document.getElementById(id)
    const checked = checkbox.checked;



    var selectedTent = tents.filter(item => Number(item.id) === Number(item_id))
    var priceSelected = 0;
    if (isSpecialDay(item_id)) {
      priceSelected = selectedTent[0].price.toFixed(1) * selectedTent[0].qtypeople;
    } else {
      priceSelected = selectedTent[0].price_date.toFixed(1) * selectedTent[0].qtypeople;
    }
    if (checked) {
      setSelectedTents(prevSelectedTents => [...prevSelectedTents, {"id": Number(item_id), "priceTent": priceSelected, "qtypeople": selectedTent[0].qtypeople, "Title": selectedTent[0].tentTitle}]);
      setTotalImportTents(totalImportTents + priceSelected);
    } else {
      var arraySliced = [];
      arraySliced = SelectedTents.filter(item => item.id !== Number(item_id))
      setSelectedTents([...arraySliced]);
      setTotalImportTents(totalImportTents - (priceSelected));
      if (arraySliced.length === 0) {
        setTotalImportTents(0);
      }
    }
  }

  const triggerExtras = (event) => {
    event.preventDefault();
    localStorage.setItem("idtents", JSON.stringify(SelectedTents));
    localStorage.setItem("totalImportTents", JSON.stringify(totalImportTents));
    localStorage.setItem("AditionalPeople", JSON.stringify(qtyAditionalAdults))
    if (CheckTotalPeople() === true) {
      setPropCart("idtents", SelectedTents);
      setPropCart("totalImportTents", totalImportTents);
      setPropCart("AditionalPeople", qtyAditionalAdults);
      navigate('/cart-extras');
    }
  };

  const [MessageAmountPeople, setMessageAmountPeople] = useState(null);

  const CheckTotalPeople = () => {
    var state = true;
    var totalTentPeople = 0;
    for (let tent of SelectedTents) {
      totalTentPeople += tent.qtypeople;
    }
    var totalCart = cart.qtyAdults + cart.qtyKids;
    if (totalTentPeople < totalCart && qtyAditionalAdults === 0) {
      state = false;
      setMessageAmountPeople("Agrega otra tienda capacidad de Personas superada.");
    } else if (qtyAditionalAdults !== 0) {
      totalTentPeople += qtyAditionalAdults;
      if (totalTentPeople < totalCart && qtyAditionalAdults) {
        state = false;
        setMessageAmountPeople("Agrega otra tienda capacidad de Personas superada.");
      }
    }
    return state;
  }

  const isSpecialDay = (id) => {
    const today = new Date()
    let tent_filtered = tents.filter(item => Number(item.id) === Number(id))[0]
    if ((cart.checkInDate <= tent_filtered.price_from_date || cart.checkInDate <= tent_filtered.price_to_date) && (cart.checkOutDate >= tent_filtered.price_from_date || cart.checkOutDate >= tent_filtered.price_to_date)) {
      return false;
    }
    return true;
  }

  /*******************************NOTIFCATION VARIABLES************************************************/
  const [typeNotification, setTypeNotification] = useState("Success");
  const [MessageNotification, setMessageNotification] = useState("No Message");
  const [TriggerNotification, setTriggerNotification] = useState(false);

  const AppearNotification = async () => {
    await new Promise((r) => setTimeout(r, 4000));
    await setTriggerNotification(false);
  };

  useEffect(() => {
    if (MessageAmountPeople !== null) {
      setTypeNotification("Error");
      setMessageNotification(MessageAmountPeople);
      setTriggerNotification(true);
      AppearNotification();
      setMessageAmountPeople(null);
    }
  }, [MessageAmountPeople])

  return (
    <>
      <CartFirstSectionContainer>
        <NotificationBandSection type={typeNotification} message={MessageNotification} trigger={TriggerNotification} />
        <CartFirstSectionWrapper>
          <CartHeader>Haz seleccionado desde las fechas {Moment(cart.checkInDate).format('DD-MM-YYYY')} hasta las fecha {Moment(cart.checkOutDate).format('DD-MM-YYYY')} se encuentran disponibles las siguientes habitaciones</CartHeader>
          {tents.length !== 0 && (
            tents.map((item) => {
              return (
                <>
                  {AvailableTents.includes(item.id) && (
                    <CartRoomSection id={item.id}>
                      <CartDescriptionWrapper>
                        <CarouselImage>
                          <Carousel SliderData={item.imgroutes} dataImages={images[item.id]} />
                        </CarouselImage>
                        <CarosuelSkip>
                          <CarouselButtons nextSlide={nextSlide} prevSlide={prevSlide} dataId={item.id} />
                        </CarosuelSkip>
                        <DescriptionGridDetail>
                          <DescriptionRowHeader>{item.tentTitle}</DescriptionRowHeader>
                          <DescriptionRow><FaBed />{item.description.bed}</DescriptionRow>
                          <DescriptionRow><FaUserFriends /> {item.qtypeople} Adultos  y {item.qtykids} Niños, Hasta 1 persona Adicional</DescriptionRow>
                          <DescriptionRow><FaPhone /> {item.description.services}</DescriptionRow>
                          <DescriptionRow><FaStream /> {item.description.description}</DescriptionRow>
                        </DescriptionGridDetail>
                      </CartDescriptionWrapper>
                      <CartDetailWrapper>
                        <SmallHeader>Desayuno Incluido por PEN {isSpecialDay(item.id) ? item.price.toFixed(2) : item.price_date.toFixed(2)}</SmallHeader>
                        <CartDescriptionGridPriceRow>
                          <Column1Header>
                            <HeaderP>Tarifas (Todo Incluido)</HeaderP>
                          </Column1Header>
                          <Column1>
                            <ColP>Tarifa 100% FLEXIBLE</ColP>
                            <ColP>100% cancelación gratuita</ColP>
                          </Column1>
                          <Column2Header>
                            <HeaderP>Precio por persona/noche</HeaderP>
                          </Column2Header>
                          <Column2>
                            <Collabel></Collabel>
                            <Colh1>{isSpecialDay(item.id) ? item.price.toFixed(2) : item.price_date.toFixed(2)}</Colh1>
                          </Column2>
                          <Column3Header>
                            <HeaderP>Precio total</HeaderP>
                          </Column3Header>
                          <Column3>
                            <Collabel></Collabel>
                            <Colh1>{isSpecialDay(item.id) ? item.price.toFixed(1) * item.qtypeople : item.price_date.toFixed(1) * item.qtypeople}</Colh1>
                          </Column3>
                          <Column4>
                            <ButtonAddTent>
                              <ButtonAdd id={"TENT" + item.id} onClick={(event) => {triggerAddTent(event)}} />
                              <LabelButton>Añadir a mi reserva</LabelButton>
                            </ButtonAddTent>
                            <AditionalPeopleComponent>
                              <LableAditionalPeople>Agregar Persona Adicional 100PEN</LableAditionalPeople>
                              <ButtonAdd id={"TENT_ADPEOPLE_" + item.id} onChange={(event) => {AditionalPeople(event)}} />
                            </AditionalPeopleComponent>
                          </Column4>
                        </CartDescriptionGridPriceRow>
                      </CartDetailWrapper>
                    </CartRoomSection>
                  )}
                </>
              )
            })
          )}
        </CartFirstSectionWrapper >
      </CartFirstSectionContainer>
      <SideBottombarContainer isOpen={isOpen} onClick={triggerOpen}>
        <SideBottombarColumn2>
          <ExtraWrapperHeader>
            <ExtraHeaderColumn1>
              <HeaderExtracolumn1P4>Total con o extras:</HeaderExtracolumn1P4>
              <HeaderExtracolumn1h2> {totalImportTents}</HeaderExtracolumn1h2>
              <HeaderExtracolumn1P3>{totalImportTents} </HeaderExtracolumn1P3>
            </ExtraHeaderColumn1>
            <ExtraHeaderColumn2>
              <ButtonCartForwardExtra onClick={(event) => {triggerExtras(event)}}>Continuar con la Reserva</ButtonCartForwardExtra>
            </ExtraHeaderColumn2>
          </ExtraWrapperHeader>
          {SelectedTents.length !== 0 && (
            SelectedTents.map((item) => {
              return (
                <>
                  <ItemListExtras subSection={'tent'}>
                    <ItemListExtraTitle2 subSection={'tent'}>{item.Title}</ItemListExtraTitle2>
                    <ItemListExtraPrice>{item.priceTent.toFixed(1)}</ItemListExtraPrice>
                    <ItemListExtraQty>1</ItemListExtraQty>
                  </ItemListExtras>
                </>
              )
            }))}
        </SideBottombarColumn2>
        <SideBottombarColumn1>
          <ListExtrasHeader>
            <ListExtraActivityHeader>
              Actividades extras y Productos Seleccionados
            </ListExtraActivityHeader>
          </ListExtrasHeader>
        </SideBottombarColumn1>
      </SideBottombarContainer>
    </>

  )
}

