export const ButtonAccess = styled(Link)`
  background: ${({primary}) => (primary ? 'var(--White)' : 'var(--White)')};
  white-space:nowrap;
  padding:${({big}) => (big ? '14px 38px' : '10px 15px')};
  color:${({dark}) => (dark ? 'var(--GreenStrong)' : 'var(--GreenStrong)')};
  font-size:${({fontBig}) => (fontBig ? '20px' : '14px')};
  outline:none;
  text-decoration:none;
  border:2px solid var(--GreenStrong); 
  cursor:pointer;
  display:flex;
  width:200px;
  justify-content:center;
  align-items:center;
  transition: all 0.2s ease-in-out;

  &:hover{
    transition: all 0.2s ease-in-out;
    background: ${({primary}) => (primary ? 'var(--GreenStrong)' : 'var(--GreenStrong)')};
    color:${({dark}) => (dark ? 'var(--White)' : 'var(--White)')};
  }
  @media screen and (max-width: 723px){
    transition: all 0.2s ease-in-out;
    width:180px;
  }
  @media screen and (max-width: 480px){
    transition: all 0.2s ease-in-out;
    width:200px;
  }
`
