export const InputOption = styled.input.attrs({type: 'checkbox'})`
    position: relative;
	  cursor: pointer;
    margin-right:20px;
    transition: all 0.2s ease-in-out;

    &:before{
      content: "";
      display: block;
      position: absolute;
      width: 30px;
      height: 30px;
      top: 0;
      left: 0;
      background-color:#e9e9e9;
    transition: all 0.2s ease-in-out;
    }
    &:checked:before{
     content: "";
     display: block;
     position: absolute;
     width: 30px;
     height: 30px;
     top: 0;
     left: 0;
     background-color:#00AAA9;
    transition: all 0.2s ease-in-out;
    }
    &:checked:after{
      content: "";
      display: block;
      width: 7.5px;
      height: 15px;
      border: solid white;
      border-width: 0 3px 3px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
      position: absolute;
      top: 3px;
      left: 8px;
      transition: all 0.2s ease-in-out;
    }
`
