import React from 'react';
import {InfoContainer, InfoWrapper, InfoRow, Column1, Column2, TextWrapper, TopLine, Heading, Subtitle, ImgWrap, Img, TableWrap, Table, HeaderTable, RowTable, Labelp} from './ExperienceSectionInfoElements';
import {Promotions, HeaderPromotion, ListofBulltes, Bullets, PricePromotion} from './ExperienceSectionInfoElements';
import {ExperiencesObject} from '../../Data/Experiences/ExperiencesData';

const ExperienceSectionInfo = ({keyword}) => {
  return (
    <>
      {ExperiencesObject[keyword].map((item, index) => (
        <InfoContainer lightBg={item.lightBg} id={item.id} marginTopBot={item.marginTopBot} key={index}>
          <InfoWrapper>
            <InfoRow imgStart={item.imgStart}>
              <Column1>
                <TextWrapper>
                  <TopLine darkText={item.darkText}>{item.topLine}</TopLine>
                  <Heading lightText={item.lightText}>{item.headline}</Heading>
                  <Subtitle darkText={item.darkText}>{item.description}</Subtitle>
                  {item.hasOwnProperty('promociones') === true && (
                    item.promociones.length !== 0 && (
                      item.promociones.map((promo) => (
                        <Promotions>
                          <HeaderPromotion darkText={item.darkText} orientation={promo.orienationLeft}>
                            {promo.Header}
                          </HeaderPromotion>
                          <ListofBulltes lightText={item.lightText} orientation={promo.orienationLeft}>
                            {promo.bulletspoints.map((bullet) => (
                              <Bullets lightText={item.lightText} orientation={promo.orienationLeft}>
                                {bullet}
                              </Bullets>
                            ))}
                          </ListofBulltes>
                          <PricePromotion darkText={item.darkText} orientation={promo.orienationLeft}>{promo.price}</PricePromotion>
                        </Promotions>
                      ))
                    )
                  )}
                </TextWrapper>
              </Column1>
              <Column2>
                {item.img !== "table" && (
                  <ImgWrap>
                    <Img src={item.img} alt={item.alt} imgCircle={item.imgCircle} />
                  </ImgWrap>
                )}
                {item.img === "table" && (
                  <TableWrap>
                    <Table>
                      <tr>
                        <HeaderTable>{item.tableHeader[0].name}</HeaderTable>
                        <HeaderTable>{item.tableHeader[0].price}</HeaderTable>
                      </tr>
                      {item.table.map((tablerowitem, index) => (
                        <tr>
                          <RowTable>{tablerowitem.name}</RowTable>
                          <RowTable>S/{tablerowitem.price}.00</RowTable>
                        </tr>
                      ))}
                    </Table>
                    <Labelp>*Precios Referenciales, los precios actualizados los encuentras en la seccion de extras.</Labelp>
                  </TableWrap>
                )}
              </Column2>
            </InfoRow>
          </InfoWrapper>
        </InfoContainer>
      ))}
    </>
  );
};

export default ExperienceSectionInfo;
