import React, {useState, useEffect} from 'react';
import {useNavigate} from "react-router-dom";
import {ButtonCartForwardExtra} from '../../ButtonElements';
import {CartSecondSectionContainer, CartSecondSectionWrapper, FilterSectionWrapper, ExtrasSection, HeaderExtra, ExtraRow, ExtraImg, Column1, Header, Title, Description, ExtraPrice, ButtonExtrawrapper, ButtonExtra, LabelExtra, Column2, FilterHeader, FHeader, FilterOptions, FilterOpt, FilterCheck, FilterLabel, LabelTopFilter, EraseFilter, Filter, SideBottombarContainer, SideBottombarColumn1, SideBottombarColumn2, ButtonExtraForward, ExtraHeaderColumn2, ExtraHeaderColumn1, HeaderExtracolumn1P4, HeaderExtracolumn1h2, HeaderExtracolumn1P3, ListExtras, ListExtrasHeader, ExtraWrapperHeader, ItemListExtras, ItemListExtraTitle, ItemListExtraDescrp, ItemListExtraPrice, ItemListExtraQty, ItemListExtraUnitPrice, ListExtraActivityHeader, ItemListExtraTitle2, TextLabel, Number, CounterWrapper, SmallBlocks, Blocks, ButtonDecrease, ButtonIncrement, ExtraDuration} from './CartSecondSection';

import {HeaderExtraSpecific, ExtraRowCarousel, ExtraRowItem, ExtraRowItemContent, ExtraRowImage, ExtraRowName, ExtraRowDescp, ExtraRowbtns, ArrowLeft, ArrowRight, ExtraRowWrapperCarousel, ContainerExtraRowWrapper, ExtraRowPrice, ExtraRowNameHeader, CounterWrapperProduct, BlocksProduct, SmallBlocksProduct, ButtonIncrementProduct, ButtonDecreaseProduct, TextLabelProduct, NumberProduct, ButtonExtrawrapperProduct, ButtonExtraProduct, LabelExtraProduct} from './CartSecondSection';

export const CartSecondSection = ({cart, setPropCart, dataExperiences, dataProducts, dataExperiencesCat}) => {

  const [isOpen, setIsOpen] = useState(false)
  const [experienceCart, setExperienceCart] = useState([])
  const [productsCart, setProductsCart] = useState([])

  const triggerOpen = () => {
    setIsOpen(!isOpen)
  }
  const triggerExtra = (event) => {
    const id = event.target.id
    const NumberNum = document.getElementById(`NUM${id}`);
    const PriceNum = document.getElementById(`PRC${id}`)
    const Header = document.getElementById(`HEAD${id}`)
    const Title = document.getElementById(`TITLE${id}`)
    const qtyExtra = parseInt(NumberNum.innerHTML);
    const unitprice = parseFloat(PriceNum.innerHTML);
    const headerExtra = Header.innerHTML;
    const titleExtra = Title.innerHTML;

    const checkbox = document.getElementById(id)
    const checked = checkbox.checked;
    var elementIndex = null;
    if (id[0] === 'E') {
      elementIndex = experienceCart.findIndex(element => element.id === id);
    } else if (id[0] === 'P') {
      elementIndex = productsCart.findIndex(element => element.id === id);
    }
    if (checked) {
      if (elementIndex != -1) {
        if (id[0] === 'E') {
          setExperienceCart(
            experienceCart.map(item =>
              item.id === elementIndex ? {...item, qtyExperience: qtyExtra, unitprice, headerExtra, titleExtra} : item
            ))
        } else if (id[0] === 'P') {
          setProductsCart(
            productsCart.map(item =>
              item.id === elementIndex ? {...item, qtyProduct: qtyExtra, unitprice, headerExtra, titleExtra} : item
            ))
        }
      } else {
        if (id[0] === 'E') {
          setExperienceCart(prevExperienceCart => [...prevExperienceCart, {'id': id, 'qtyExperience': qtyExtra, unitprice, headerExtra, titleExtra}]);
        } else if (id[0] === 'P') {
          setProductsCart(prevProductsCart => [...prevProductsCart, {'id': id, 'qtyProduct': qtyExtra, unitprice, headerExtra, titleExtra}]);
        }
      }
    } else {
      var arraySliced = null;
      if (id[0] === 'E') {
        arraySliced = experienceCart.filter(item => item.id !== id)
        setExperienceCart([...arraySliced]);
      } else if (id[0] === 'P') {
        arraySliced = productsCart.filter(item => item.id !== id)
        setProductsCart(arraySliced);
      }
    }
  }

  const triggerQuantity = (event) => {
    const id = event.target.id
    const subText = `NUM${id.substring(3)}`
    const NumberLabel = document.getElementById(subText);
    const NumberNum = parseInt(NumberLabel.innerHTML);
    if (id[0] === 'I') {
      if (NumberNum < 12) {
        var IncrementValue = null;
        IncrementValue = NumberNum + 1;
        NumberLabel.innerHTML = IncrementValue.toString();
      }
    } else if (id[0] === 'D') {
      if (NumberNum > 1) {
        var DecrementValue = null;
        DecrementValue = NumberNum - 1;
        NumberLabel.innerHTML = DecrementValue.toString();
      }
    }
  }

  const [totalExtras, setTotalExtrasAmount] = useState(0)

  const setLocalData = (Etotal) => {
    localStorage.setItem("experiences", JSON.stringify(experienceCart));
    localStorage.setItem("products", JSON.stringify(productsCart));
    localStorage.setItem("AmountTotal", JSON.stringify(Etotal + cart.totalImportTents * cart.qtyAdults));
  }

  useEffect(() => {
    var Etotal = 0;
    for (let experience of experienceCart) {
      Etotal += (experience.unitprice * experience.qtyExperience)
    }
    for (let products of productsCart) {
      Etotal += (products.unitprice * products.qtyProduct)
    }
    setTotalExtrasAmount(Etotal);
    setPropCart("experiences", experienceCart);
    setPropCart("products", productsCart);
    setPropCart("AmountTotal", Etotal + cart.totalImportTents);
    setLocalData(Etotal);
  }, [experienceCart, productsCart])

  let navigate = useNavigate();

  const triggerAddress = (event) => {
    event.preventDefault();
    navigate('/cart-payment');
  }

  const [filter, setFilter] = useState(['EE', 'EA'])

  const triggerFilterExtra = (event) => {
    var id = event.target.id;
    var checkbox = document.getElementById(id)
    const checked = checkbox.checked;
    filter.forEach(function (element) {
      var object = document.getElementById(element)
      object.checked = false;
    });
    setFilter([])
    if (checked) {
      if (!filter.includes(id)) {
        setFilter(prevFilter => [
          ...prevFilter,
          id
        ])
      }
    } else {
      var arraySliced = filter.filter(item => item !== id)
      setFilter(arraySliced)
    }
  }

  const eraseFilter = () => {
    filter.forEach(function (element) {
      var object = document.getElementById(element)
      object.checked = false;
    });
    setFilter(['EE', 'EA'])
  }

  const [containerPosition, setContainerPosition] = useState(0);
  const [permissibleLenght, setPermissibleLenght] = useState(0);
  const [lenghtElementsCard, setElementsCardLength] = useState(0);

  function vwTOpx() {
    var value = 0;
    var d = document
    var e = d.documentElement
    var g = d.getElementsByTagName('body')[0]
    var x = window.hinnerWidth || e.clientWidth || g.clientWidth

    var result = 0;
    console.log(x)
    if (x >= 480) {
      value = 15;
      result = (x * value) / 100;
      result = result + 20;
    } else {
      value = 60;
      result = (x * value) / 100;
      result = result + 20;
    }


    return result;
  }

  useEffect(() => {
    var AllcardsElements = dataProducts.length > 0 ? dataProducts.length : 1;
    var element = dataProducts.length > 0 ? vwTOpx() : 1;
    var elementContainer = document.getElementById('ContainerProducts').offsetWidth;
    var elementsInside = elementContainer / element;
    setPermissibleLenght((AllcardsElements - elementsInside) * element);
    setElementsCardLength(element);
  }, [])


  const moveLeft = () => {
    if ((containerPosition + lenghtElementsCard) <= 0) {
      var ElementContainer = document.getElementById("ContainerProducts");
      ElementContainer.style.transform = "translateX(" + (containerPosition + lenghtElementsCard) + "px)";
      setContainerPosition(containerPosition + lenghtElementsCard);
    }
  }


  const moveRight = () => {
    if ((containerPosition - lenghtElementsCard) >= (-1 * permissibleLenght - lenghtElementsCard)) {
      var ElementContainer = document.getElementById("ContainerProducts");
      ElementContainer.style.transform = "translateX(" + (containerPosition - lenghtElementsCard) + "px)";
      setContainerPosition(containerPosition - lenghtElementsCard);
    }
  }



  return (
    <>
      <CartSecondSectionContainer>
        <CartSecondSectionWrapper>
          <FilterSectionWrapper>
            <Filter>
              <LabelTopFilter>Filtros</LabelTopFilter>
              <EraseFilter onClick={eraseFilter}>Borrar Filtros</EraseFilter>
            </Filter>
            <FilterHeader>
              <FHeader>¿Que Tipo de Extra Quieres?</FHeader>
            </FilterHeader>
            <FilterOptions>
              {dataExperiencesCat.map((category) => (

                <FilterOpt>
                  <FilterCheck id={category.subcategory} onClick={(event) => {triggerFilterExtra(event)}} />
                  <FilterLabel>{category.Header}</FilterLabel>
                </FilterOpt>
              ))}
            </FilterOptions>
          </FilterSectionWrapper>
          <ExtrasSection>
            <HeaderExtra>Packs Para tu Estadia</HeaderExtra>
            <HeaderExtraSpecific>Prueba nuestros Productos</HeaderExtraSpecific>
            <ExtraRowCarousel>
              <ArrowLeft onClick={moveLeft} />
              <ExtraRowWrapperCarousel>
                <ContainerExtraRowWrapper id="ContainerProducts">
                  {dataProducts.map((item) => {
                    return (
                      <ExtraRowItem className="CardsProducts">
                        <ExtraRowItemContent>
                          <ExtraRowImage imgsrc={item.imgroutes}></ExtraRowImage>
                          <ExtraRowNameHeader id={"HEAD" + item.category + item.idproduct}>Producto</ExtraRowNameHeader>
                          <ExtraRowName id={"TITLE" + item.category + item.idproduct}>{item.name}</ExtraRowName>
                          <ExtraRowDescp id={"TIME" + item.category + item.idproduct}>{item.description}</ExtraRowDescp>
                          <ExtraRowPrice id={"PRC" + item.category + item.idproduct}>{item.price}</ExtraRowPrice>
                          <ExtraRowbtns>
                            <CounterWrapperProduct>
                              <BlocksProduct gridarea='A'>
                                <NumberProduct id={"NUM" + item.category + item.idproduct}>1</NumberProduct>
                                <TextLabelProduct>Cantidad</TextLabelProduct>
                              </BlocksProduct>
                              <SmallBlocksProduct gridarea='C'>
                                <ButtonIncrementProduct id={"INC" + item.category + item.idproduct} onClick={(event) => {triggerQuantity(event)}}>+</ButtonIncrementProduct>
                                <ButtonDecreaseProduct id={"DEC" + item.category + item.idproduct} onClick={(event) => {triggerQuantity(event)}}>-</ButtonDecreaseProduct>
                              </SmallBlocksProduct>
                            </CounterWrapperProduct>
                            <ButtonExtrawrapperProduct>
                              <ButtonExtraProduct id={item.category + item.idproduct} onClick={(event) => {triggerExtra(event)}} />
                              <LabelExtraProduct>Añadir a mi reserva</LabelExtraProduct>
                            </ButtonExtrawrapperProduct>
                          </ExtraRowbtns>
                        </ExtraRowItemContent>
                      </ExtraRowItem>
                    )
                  })}
                </ContainerExtraRowWrapper>
              </ExtraRowWrapperCarousel>
              <ArrowRight onClick={moveRight} />
            </ExtraRowCarousel>
            <HeaderExtraSpecific>Disfruta de todas nuestras Experiencias</HeaderExtraSpecific>
            {dataExperiences.map((item) => {
              return (
                <>
                  {filter.includes(item.subcategory) && (
                    <ExtraRow id={item.idexperiences}>
                      <ExtraImg imgsrc={item.imgRoute} />
                      <Column1>
                        <Header id={"HEAD" + item.category + item.idexperiences}>{item.Header}</Header>
                        <Title id={"TITLE" + item.category + item.idexperiences}>{item.Title}</Title>
                        <Description>{item.description.description}</Description>
                        <ExtraDuration id={"TIME" + item.category + item.idexperiences}>{item.duration}</ExtraDuration>
                        <ExtraPrice id={"PRC" + item.category + item.idexperiences}>{item.price.toFixed(2)}</ExtraPrice>
                      </Column1>
                      <Column2>
                        <CounterWrapper>
                          <Blocks gridarea='A'>
                            <Number id={"NUM" + item.category + item.idexperiences}>1</Number>
                            <TextLabel>Cantidad</TextLabel>
                          </Blocks>
                          <SmallBlocks gridarea='C'>
                            <ButtonIncrement id={"INC" + item.category + item.idexperiences} onClick={(event) => {triggerQuantity(event)}}>+</ButtonIncrement>
                            <ButtonDecrease id={"DEC" + item.category + item.idexperiences} onClick={(event) => {triggerQuantity(event)}}>-</ButtonDecrease>
                          </SmallBlocks>
                        </CounterWrapper>
                        <ButtonExtrawrapper>
                          <ButtonExtra id={item.category + item.idexperiences} onClick={(event) => {triggerExtra(event)}} />
                          <LabelExtra>Añadir a mi reserva</LabelExtra>
                        </ButtonExtrawrapper>
                      </Column2>
                    </ExtraRow>
                  )}
                </>
              )
            })}
          </ExtrasSection>
        </CartSecondSectionWrapper>
      </CartSecondSectionContainer>
      <SideBottombarContainer isOpen={isOpen} onClick={triggerOpen}>

        <SideBottombarColumn2>
          <ExtraWrapperHeader>
            <ExtraHeaderColumn1>
              <HeaderExtracolumn1P4>Total con o extras:</HeaderExtracolumn1P4>
              <HeaderExtracolumn1h2> {cart.totalImportTents + totalExtras}</HeaderExtracolumn1h2>
              <HeaderExtracolumn1P3>{cart.totalImportTents} </HeaderExtracolumn1P3>
            </ExtraHeaderColumn1>
            <ExtraHeaderColumn2>
              <ButtonCartForwardExtra onClick={(event) => {triggerAddress(event)}}>Continuar con la Reserva</ButtonCartForwardExtra>
            </ExtraHeaderColumn2>
          </ExtraWrapperHeader>
          {cart.idtents.length !== 0 && (
            cart.idtents.map((item) => {
              return (
                <>
                  <ItemListExtras subSection={'tent'}>
                    <ItemListExtraTitle2 subSection={'tent'}>{item.Title}</ItemListExtraTitle2>
                    <ItemListExtraPrice>{item.priceTent.toFixed(1)}</ItemListExtraPrice>
                    <ItemListExtraQty>1</ItemListExtraQty>
                  </ItemListExtras>
                </>
              )
            }))}
        </SideBottombarColumn2>

        <SideBottombarColumn1>
          <ListExtrasHeader>
            <ListExtraActivityHeader>
              Actividades extras y Productos Seleccionados
            </ListExtraActivityHeader>
            {experienceCart.map((item) => (
              <ItemListExtras subSection={'extra'}>
                <ItemListExtraTitle>{item.headerExtra}</ItemListExtraTitle>
                <ItemListExtraDescrp>{item.titleExtra}</ItemListExtraDescrp>
                <ItemListExtraPrice>{item.unitprice * item.qtyExperience}</ItemListExtraPrice>
                <ItemListExtraQty>{item.qtyExperience}</ItemListExtraQty>
                <ItemListExtraUnitPrice>{item.unitprice}</ItemListExtraUnitPrice>
              </ItemListExtras>
            ))}
            {productsCart.map((item) => (
              <ItemListExtras subSection={'extra'}>
                <ItemListExtraTitle>{item.headerExtra}</ItemListExtraTitle>
                <ItemListExtraDescrp>{item.titleExtra}</ItemListExtraDescrp>
                <ItemListExtraPrice>{item.unitprice * item.qtyProduct}</ItemListExtraPrice>
                <ItemListExtraQty>{item.qtyProduct}</ItemListExtraQty>
                <ItemListExtraUnitPrice>{item.unitprice}</ItemListExtraUnitPrice>
              </ItemListExtras>
            ))}
          </ListExtrasHeader>
        </SideBottombarColumn1>
      </SideBottombarContainer>
    </>
  )
}

