import React from 'react';
import {InfoContainer, InfoWrapper, InfoRow, Column1, Column2, TextWrapper, TopLine, Heading, Subtitle, ImgWrap, Img} from './ExperienceSection';
import {UsObject} from '../../Data/Us';

const ExperienceSection = () => {
  return (
    <>
      {UsObject.family.map((item, index) => (
        <InfoContainer lightBg={item.lightBg} id={item.id} marginTopBot={item.marginTopBot} key={index}>
          <InfoWrapper>
            <InfoRow imgStart={item.imgStart}>
              <Column1>
                <TextWrapper>
                  <TopLine darkText={item.darkText}>{item.topLine}</TopLine>
                  <Heading lightText={item.lightText}>{item.headline}</Heading>
                  <Subtitle darkText={item.darkText}>{item.description}</Subtitle>
                </TextWrapper>
              </Column1>
              <Column2>
                <ImgWrap>
                  <Img src={item.img} alt={item.alt} />
                </ImgWrap>
              </Column2>
            </InfoRow>
          </InfoWrapper>
        </InfoContainer>
      ))}
    </>
  )
};

export default ExperienceSection;
