import React, {useState, useEffect} from 'react';
import ImageLogo from '../../../images/Banner/Logo1.png'
import {NavbarContainer, MobileIcon, Logo, NavSection, NavMenuSection, NavButton, NavLogoSection, BackgroundLogo} from './NavBarElements';
import {animateScroll as scroll} from 'react-scroll';
import {ProgressBarComponent} from '../ProgressBar';

const NavCart = ({toggle, section, step}) => {
  const [scrollNav, setScrollNav] = useState('true')

  const changeNav = () => {
    if (window.scrollY >= 80) {
      setScrollNav('false')
    } else {
      setScrollNav('true')
    }
  }

  const toggleHome = () => {
    scroll.scrollToTop();
  };

  useEffect(() => {
    window.addEventListener('scroll', changeNav);
  }, [])
  return (
    <>
      <NavSection scrollnav={scrollNav} section={section}>
        <NavbarContainer>
          <NavLogoSection to="/" onClick={toggleHome} scrollnav={scrollNav} >
            <BackgroundLogo>
              <Logo src={ImageLogo} alt="Logo" />
            </BackgroundLogo>
          </NavLogoSection>
          <NavMenuSection>
            <ProgressBarComponent step={step} />
          </NavMenuSection>
        </NavbarContainer>
      </NavSection>
    </>
  );
};

export default NavCart;
