import * as React from 'react';
import {useState, useEffect} from 'react';
import styled, {css} from 'styled-components';
import axios from 'axios';
//import {PATH_PREFIX_BACKEND} from "../Data/EnvironmentVariables"

const Frame = styled.div`
  position:absolute;
  top:60px;
  left: ${({CalendarType}) => (CalendarType === "CheckIn" ? '22%' : '44%')};
  width: 400px;
  background:var(--White);
  border: 1px solid lightgrey;
  box-shadow: 2px 2px 2px #eee;
  border-radius:10px;
  display: ${({OpenCalendar}) => (OpenCalendar ? 'block' : 'none')};

  @media screen and (max-width: 768px){
   left: ${({CalendarType}) => (CalendarType === "CheckIn" ? '0' : '20%')};
   width: 80%;
   top:100px;
  }
  @media screen and (max-width:480px){
   left: ${({CalendarType}) => (CalendarType === "CheckIn" ? '0' : '0')};
   width: 100%;
   top:100px;
  }
`;

const Header = styled.div`
  font-size: 14px;
  font-weight: bold;
  padding: 10px 10px 5px 10px;
  display: flex;
  justify-content: space-between;
  background-color: var(--PinkStrong);
  color:var(--White);
`;

const Button = styled.div`
  cursor: pointer;
`;

const Body = styled.div`
  width: 100%;
  font-size: 14px;
  color:var(--GreenStrong);
  display: flex;
  flex-wrap: wrap;
`;

const Day = styled.div`
  width: 14.2%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  ${props =>
    props.isToday &&
    css`
      border: 1px solid #eee;
    `}

  ${props =>
    props.isSelected &&
    css`
      background-color: #00AAA9;
      color:#fff;
    `}

  ${props =>
    props.isDisabled &&
    css`
      background-color: #eee;
      pointer-events:none;
    `}

`;

export const Calendar = ({OpenCalendar, CalendarType, changeDate, referenceDate}) => {
  const DAYS = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
  const DAYS_LEAP = [31, 29, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];
  const DAYS_OF_THE_WEEK = ['LUN', 'MAR', 'MIE', 'JUE', 'VIE', 'SAB', 'DOM'];
  const MONTHS = ['ENE', 'FEB', 'MAR', 'ABR', 'MAY', 'JUN', 'JUL', 'AUG', 'SEP', 'OCT', 'NOV', 'DIC'];

  const today = new Date();
  const [date, setDate] = useState(today);
  const [day, setDay] = useState(date.getDate());
  const [month, setMonth] = useState(date.getMonth());
  const [year, setYear] = useState(date.getFullYear());

  const getStartDayOfMonth = (date) => {
    return new Date(date.getFullYear(), date.getMonth(), 1).getDay();
  }

  const [startDay, setStartDay] = useState(getStartDayOfMonth(date));

  const isLeapYear = (year) => {
    return (year % 4 === 0 && year % 100 !== 0) || year % 400 === 0;
  }

  useEffect(() => {
    setDay(date.getDate());
    setMonth(date.getMonth());
    setYear(date.getFullYear());
    setStartDay(getStartDayOfMonth(date));
  }, [date]);

  const days = isLeapYear(date.getFullYear()) ? DAYS_LEAP : DAYS;

  return (
    <Frame OpenCalendar={OpenCalendar} CalendarType={CalendarType}>
      <Header>
        <Button onClick={() => setDate(new Date(year, month - 1, day))}>Previo</Button>
        <div>
          {MONTHS[month]} {year}
        </div>
        <Button onClick={() => setDate(new Date(year, month + 1, day))}>Siguiente</Button>
      </Header>
      <Body>
        {DAYS_OF_THE_WEEK.map(d => (
          <Day key={d}>
            <strong>{d}</strong>
          </Day>
        ))}
        {Array(days[month] + (startDay - 1))
          .fill(null)
          .map((_, index) => {
            const d = index - (startDay - 2);
            return (
              <Day
                key={index}
                isToday={d === today.getDate() && month === today.getMonth() && year === today.getFullYear()}
                isSelected={d === day}
                isDisabled={d < today.getDate() && month === today.getMonth()}
                onClick={() => {
                  var bufferDate = new Date(year, month, d);
                  changeDate(bufferDate)
                  setDate(bufferDate)
                }}
              >
                {d > 0 ? d : ''}
              </Day>
            );
          })}
      </Body>
    </Frame>
  );
}
