import React, {useState, useEffect} from 'react';
import Navbar from '../components/Navbar';
import HeroSection from '../components/HeroSection';
import Band from '../components/Band';
import Experiences from '../components/Experiences';
import Footer from '../components/Footer';
import Promotion from '../components/Promotions';
import ContactUs from '../components/ContactUs';
import LoadingPage from '../components/LoadingPage';
import ExperienceSection from '../components/ExperienceSection/index';
import SidebarSection from '../components/SideBar';
import FloatingButtonComponent from '../components/FloatingButton';
import axios from 'axios';
import {PATH_PREFIX_BACKEND} from "../Data/EnvironmentVariables"

const Home = ({cart, setPropCart, AvailableTents, setAvailableTents, user, token, CloseSession}) => {
  const [loading, setLoading] = useState(false);
  const [isOpen, setIsOpen] = useState(false)
  const [accessoAPI, setAccesoAPI] = useState({tipo: 'GET', url: PATH_PREFIX_BACKEND + '/api/promotions'});
  const [errorAPI, setErrorAPI] = useState(null);
  const [respuestaAPI, setRespuestaAPI] = useState([]);

  useEffect(() => {
    const consultaAPI = async () => {
      setErrorAPI(null);
      try {
        const consulta = await axios({method: accessoAPI.tipo, url: accessoAPI.url})
        setRespuestaAPI(consulta.data['data']);
      } catch (error) {
        setErrorAPI(error.response)
      }
      setLoading(true);
    };
    consultaAPI();
  }, [accessoAPI.tipo, accessoAPI.url])


  const toggle = () => {
    setIsOpen(!isOpen)
  }

  if (loading === false) {
    return <LoadingPage />
  } else {
    return (
      <>
        <Band />
        <Navbar toggle={toggle} section={false} user={user} CloseSession={CloseSession} />
        <SidebarSection isOpen={isOpen} toggle={toggle} user={user} CloseSession={CloseSession} />
        <HeroSection section={'index'} cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} user={user} />
        <ExperienceSection />
        <Promotion PromotionsData={respuestaAPI} user={user} cart={cart} setPropCart={setPropCart} />
        <Experiences />
        <ContactUs />
        <FloatingButtonComponent />
        <Footer />
      </>
    )
  }
}

export default Home
