import React from 'react'
import ImageLogo from '../../images/Banner/Logo1.png'
import '../../App.css';
import {LoadingPageContainer, LoadingPageWrapper, LogoLoading, Logo, BackgroundLogo,LoadingDescription} from './LoadingPageElements';
//
const LoadingPage = () => {
  return (
    <LoadingPageContainer>
      <LoadingPageWrapper>
        <LogoLoading>
            <BackgroundLogo className="App-logo">
              <Logo src={ImageLogo} />
            </BackgroundLogo>
        </LogoLoading>
        <LoadingDescription>Cargando...</LoadingDescription>
      </LoadingPageWrapper>
    </LoadingPageContainer>
  );
};


export default LoadingPage;
