import React, {useEffect} from 'react';
import {ProgressBarContainer, ProgressBar, StepProgres, StepCount, StepDescription} from './ProgressBarElements';


export const ProgressBarComponent = ({step}) => {


  useEffect(() => {
    const stringStep = '*[id^="ProgressStep"]'
    const AllStepElements = document.querySelectorAll(stringStep);
    AllStepElements.forEach(function (element) {
      element.classList.remove('isActive')
    });
    const stepElement = document.getElementById(`ProgressStep-${step}`);
    stepElement.classList.add('isActive');
  }, [step])

  return (
    <ProgressBarContainer>
      <ProgressBar>
        <StepProgres id="ProgressStep-1">
          <StepCount></StepCount>
          <StepDescription>Experiencias Escogidas</StepDescription>
        </StepProgres>
        <StepProgres id="ProgressStep-2">
          <StepCount></StepCount>
          <StepDescription>Adicionales</StepDescription>
        </StepProgres>
        <StepProgres id="ProgressStep-3">
          <StepCount></StepCount>
          <StepDescription>Reserva</StepDescription>
        </StepProgres>
        <StepProgres id="ProgressStep-4">
          <StepCount></StepCount>
          <StepDescription>Confirmación de Reserva</StepDescription>
        </StepProgres>
      </ProgressBar>
    </ProgressBarContainer>
  )
}

