import React, {useState, useEffect} from 'react';
import LoadingPage from '../components/LoadingPage';
import NavCart from '../components/CartSection/NavCart';
import HeroSection from '../components/HeroSection';
import {PATH_PREFIX_BACKEND} from "../Data/EnvironmentVariables"
import Footer from '../components/Footer';
import {CartSecondSection} from '../components/CartSection/CartSecondSection';
import axios from 'axios';

const ExtraSection = ({cart, setPropCart, AvailableTents, setAvailableTents}) => {

  const [loading, setLoading] = useState(false);
  const [accessoExperiencesAPI, setAccesoExperiencesAPI] = useState({tipo: 'GET', url: PATH_PREFIX_BACKEND + '/api/experiences'});
  const [accessoProductsAPI, setAccesoProductsAPI] = useState({tipo: 'GET', url: PATH_PREFIX_BACKEND + '/api/products'});
  const [datosExperiences, setDatosExperiences] = useState([])
  const [datosProducts, setDatosProducts] = useState([])
  const [datosExperiencesCategory, setDatosExperienceCategory] = useState([])
  const [errorExperienceCatAPI, setErrorExperienceCatAPI] = useState([])
  const [errorExperiencesAPI, setErrorExperiencesAPI] = useState(null);
  const [errorProductsAPI, setErrorProductsAPI] = useState(null);
  const [respuestaExperiencesCatAPI, setRespuestaExperiencesCatAPI] = useState({respuesta: 'KO'})
  const [respuestaExperiencesAPI, setRespuestaExperiencesAPI] = useState({respuesta: 'KO'});
  const [respuestaProductsAPI, setRespuestaProductsAPI] = useState({respuesta: 'KO'});

  useEffect(() => {
    const consultaAPI = async () => {
      setErrorExperiencesAPI(null);
      try {
        const consultaExperiences = await axios({method: accessoExperiencesAPI.tipo, url: accessoExperiencesAPI.url, data: datosExperiences})
        setRespuestaExperiencesAPI(consultaExperiences.data);
      } catch (error) {
        setErrorExperiencesAPI(error.response)
      }

      try {
        const consultaExperiencesCat = await axios({method: accessoExperiencesAPI.tipo, url: accessoExperiencesAPI.url + '/getExperienceCat'})
        setRespuestaExperiencesCatAPI(consultaExperiencesCat.data);
      } catch (error) {
        setErrorExperienceCatAPI(error.response)
      }

      try {
        const consultaProducts = await axios({method: accessoProductsAPI.tipo, url: accessoProductsAPI.url, data: datosProducts})
        console.log(consultaProducts.data)
        setRespuestaProductsAPI(consultaProducts.data);
      } catch (error) {
        setErrorProductsAPI(error.response)
      }
      setLoading(true);
    };
    consultaAPI();
  }, [accessoExperiencesAPI.tipo, accessoExperiencesAPI.url, datosExperiences])

  if (loading === false) {
    return <LoadingPage />
  } else {
    return (
      <>
        <NavCart section={true} step={2} />
        <HeroSection section={'cart-2'} cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} />
        <CartSecondSection cart={cart} setPropCart={setPropCart} dataExperiences={respuestaExperiencesAPI['data']} dataProducts={respuestaProductsAPI['data']} dataExperiencesCat={respuestaExperiencesCatAPI['data']} />
      </>
    )

  }
}

export default ExtraSection;
