import React, {useState, useEffect} from 'react';
import LoadingPage from '../components/LoadingPage';
import NavCart from '../components/CartSection/NavCart';
import HeroSection from '../components/HeroSection';
import Footer from '../components/Footer';
import {CartThirdSection} from '../components/CartSection/CartThirdSection';
import {useLocation} from "react-router-dom";
import axios from 'axios';

const AddressSection = ({token, user, cart, setPropCart, AvailableTents, setAvailableTents}) => {

  const [loading, setLoading] = useState(false);
  useEffect(() => {
    const loadData = async () => {
      await new Promise((r) => setTimeout(r, 1200));
      setLoading((loading) => !loading);
    };
    loadData();
  }, [])

  if (loading === false) {
    return <LoadingPage />
  } else {
    return (
      <>
        <NavCart section={true} step={3} />
        <HeroSection section={'cart-3'} cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} />
        <CartThirdSection token={token} user={user} cart={cart} setPropCart={setPropCart} />
        <Footer />
      </>
    )

  }
}

export default AddressSection;
