import React, {useState, useEffect} from 'react'
import {RecoverPasswordContainer, RecoverPasswordWrapper, SubmitButton, ContentWrapper, ContentForm, HeaderContentForm, SubtitleContentForm, InputDocumentation, RedLabel, InputCode, InputPassword, InputSecurePassword, Icon} from './ForgotPasswordElements';
import NotificationBandSection from '../NotificationBand';
import ImageBackground from '../../images/Background/background.png'
import axios from 'axios';
import {useNavigate} from "react-router-dom";
import {PATH_PREFIX_BACKEND} from "../../Data/EnvironmentVariables"

const RecoverPasswordSection = () => {
  let navigate = useNavigate();
  const [step, setStep] = useState(1);

  const nextStep = (event) => {
    event.preventDefault();
    if (step < 3) {
      setStep(prevStep => prevStep + 1);
    }
  }

  const [AccessoCodeAPI, setAccessoCodeAPI] = useState({tipo: 'POST', url: PATH_PREFIX_BACKEND + '/api/users/generateCode/'});
  const [respuestaAPI, setRespuestaAPI] = useState(null);
  const [errorCodeAPI, setErrorCodeAPI] = useState(null);


  const [EmailHook, setEmailHook] = useState(null)

  const GenerateCodeSendMessage = (event) => {
    event.preventDefault();
    var Email = document.getElementById("Email").value;
    setEmailHook(Email)
    const datos = {
      email: Email,
    }

    const GenerateCode = async () => {
      setErrorCodeAPI(null);
      try {
        await axios({method: AccessoCodeAPI.tipo, url: AccessoCodeAPI.url, data: datos}).then(response => {
          setRespuestaAPI(response.data)
          nextStep(event);
        })
      } catch (error) {
        setErrorCodeAPI(error.response)
      }
    };
    GenerateCode();
  }

  const [ValidationCodeNotAssert, setValidationCodeNotAssert] = useState(false);
  const [errorCodeVerification, setErrorCodeVerifcation] = useState(null);
  const [ValidateCodeAPI, setValidateCodeAPI] = useState({tipo: 'POST', url: PATH_PREFIX_BACKEND + '/api/users/validateCode/'});
  const [ValidatedCode, setValidatedCode] = useState(null);

  const ValidateCode = (event) => {
    event.preventDefault();
    var InputCode = document.getElementById("VerificationCode").value;
    const datos = {
      email: EmailHook,
      code: Number(InputCode)
    }
    const GenerateCode = async () => {
      setErrorCodeVerifcation(null);
      try {
        await axios({method: ValidateCodeAPI.tipo, url: ValidateCodeAPI.url, data: datos}).then(response => {
          if (Number(response.data.code) === Number(InputCode)) {
            setValidatedCode(Number(response.data.code))
            nextStep(event);
          } else {
            setValidationCodeNotAssert(true);
          }
        })
      } catch (error) {
        setErrorCodeVerifcation(error.response)
      }
    };
    GenerateCode();
  }


  const [AssertEqualPassword, setAssertEqualPassword] = useState(false);
  const [UpdatePasswordAPI, setUpdatePasswordAPI] = useState({tipo: 'POST', url: PATH_PREFIX_BACKEND + '/api/users/updatePassword/'});
  const [ErrorUpdatePasswordAPI, setErrorUpdatePasswordAPI] = useState(null)
  const [PasswordSuccessfullChangePasswordAPI, setPasswordSuccessfullChangePasswordAPI] = useState(null)

  const ChangePassword = (e) => {
    e.preventDefault();
    var password = document.getElementById("NewPassword").value
    var securePassword = document.getElementById("NewSecurePassword").value
    if (password === securePassword) {
      const datos = {
        email: EmailHook,
        code: ValidatedCode,
        password: password
      }
      const UpdatePassword = async () => {
        setErrorUpdatePasswordAPI(null);
        try {
          const response = await axios({method: UpdatePasswordAPI.tipo, url: UpdatePasswordAPI.url, data: datos})
          await setPasswordSuccessfullChangePasswordAPI(response.data)
          await new Promise((r) => setTimeout(r, 5000));
          await navigate('/signin');
        } catch (error) {
          setErrorUpdatePasswordAPI(error.response)
        }
      };
      UpdatePassword();
    }
    else {
      setAssertEqualPassword(true)
    }
  }

  const EmptyField = (event) => {
    var id = event.target.id;
    var element = document.getElementById(id);
    var label = document.getElementById(`L${id}`);
    if (element.value === "") {
      label.classList.remove("isActive");
      label.classList.add("isActive");
    } else {
      label.classList.remove("isActive");
    }
  }

  /*******************************NOTIFCATION VARIABLES************************************************/
  const [typeNotification, setTypeNotification] = useState("Success");
  const [MessageNotification, setMessageNotification] = useState("No Message");
  const [TriggerNotification, setTriggerNotification] = useState(false);

  const AppearNotification = async () => {
    await new Promise((r) => setTimeout(r, 4000));
    await setTriggerNotification(false);
  };

  useEffect(() => {
    if (errorCodeAPI !== null) {
      setTypeNotification("Error");
      setMessageNotification("No se ha podido encontrar tu usuario en el sistema.");
      setTriggerNotification(true);
      AppearNotification();
      setErrorCodeAPI(null);
    }
    else if (respuestaAPI !== null) {
      setTypeNotification("Success");
      setMessageNotification("Se ha enviado a tu correo el codigo de verificación.");
      setTriggerNotification(true);
      AppearNotification();
      setRespuestaAPI(null);
    }
    else if (ValidationCodeNotAssert === true) {
      setTypeNotification("Error");
      setMessageNotification("Codigo de Verificación Incorrecto.");
      setTriggerNotification(true);
      AppearNotification();
      setValidationCodeNotAssert(false);
    }
    else if (AssertEqualPassword === true) {
      setTypeNotification("Warning");
      setMessageNotification("Las contraseñas deben ser las mismas.");
      setTriggerNotification(true);
      AppearNotification();
      setAssertEqualPassword(false);
    }
    else if (PasswordSuccessfullChangePasswordAPI !== null) {
      setTypeNotification("Success");
      setMessageNotification("La contraseña de ha actualizado correctamente.");
      setTriggerNotification(true);
      AppearNotification();
      setPasswordSuccessfullChangePasswordAPI(null);
    }
    else if (ErrorUpdatePasswordAPI !== null) {
      setTypeNotification("Error");
      setMessageNotification("No se ha podido actualizar la contraseña.");
      setTriggerNotification(true);
      AppearNotification();
      setErrorUpdatePasswordAPI(null);
    }
  }, [errorCodeAPI, respuestaAPI, ValidationCodeNotAssert, AssertEqualPassword, PasswordSuccessfullChangePasswordAPI, ErrorUpdatePasswordAPI])


  if (step === 1) {
    return (
      <RecoverPasswordContainer>
        <Icon to="/">BAMBUCAMP</Icon>
        <NotificationBandSection type={typeNotification} message={MessageNotification} trigger={TriggerNotification} />
        <RecoverPasswordWrapper>
          <ContentWrapper imgsrc={ImageBackground}>
            <ContentForm onSubmit={(event) => {GenerateCodeSendMessage(event)}}>
              <HeaderContentForm>
                ¿Olvidaste tu contraseña?
              </HeaderContentForm>
              <SubtitleContentForm>
                Primero vamos a verificar tu identidad
              </SubtitleContentForm>
              <SubtitleContentForm>
                Ingresa tu correo electronico:
              </SubtitleContentForm>
              <InputDocumentation id="Email" onChange={(event) => {EmptyField(event)}} />
              <RedLabel id="LEmail" className="isActive">Ingresa tu Correo Electronico</RedLabel>
              <SubmitButton value="Continuar" />
            </ContentForm>
          </ContentWrapper>
        </RecoverPasswordWrapper>
      </RecoverPasswordContainer>

    )
  } else if (step === 2) {
    return (
      <RecoverPasswordContainer>
        <Icon to="/">BAMBUCAMP</Icon>
        <NotificationBandSection type={typeNotification} message={MessageNotification} trigger={TriggerNotification} />
        <RecoverPasswordWrapper>
          <ContentWrapper imgsrc={ImageBackground}>
            <ContentForm onSubmit={(event) => {ValidateCode(event)}}>
              <HeaderContentForm>
                Verifica que eres tú
              </HeaderContentForm>
              <SubtitleContentForm>
                Te hemos enviado un código al correo electronico proporcionado.
              </SubtitleContentForm>
              <SubtitleContentForm>
                Ingresa el código de 4 digitos enviado a tu correo.
              </SubtitleContentForm>
              <InputCode id="VerificationCode" onChange={(event) => {EmptyField(event)}} />
              <RedLabel id="LVerificationCode" className="isActive">El codigo de verificación es obligatorio.</RedLabel>
              <SubmitButton value="Continuar" />
            </ContentForm>
          </ContentWrapper>
        </RecoverPasswordWrapper>
      </RecoverPasswordContainer>
    )
  } else if (step === 3) {
    return (
      <RecoverPasswordContainer>
        <Icon to="/">BAMBUCAMP</Icon>
        <NotificationBandSection type={typeNotification} message={MessageNotification} trigger={TriggerNotification} />
        <RecoverPasswordWrapper>
          <ContentWrapper imgsrc={ImageBackground}>
            <ContentForm onSubmit={(event) => {ChangePassword(event)}}>
              <HeaderContentForm>
                Se ha validado el codigo de verificación
              </HeaderContentForm>
              <SubtitleContentForm>
                Ingrese su nueva contraseña
              </SubtitleContentForm>
              <InputPassword id="NewPassword" onChange={(event) => {EmptyField(event)}} />
              <RedLabel id="LNewPassword" className="isActive">Ingrese su contraseña</RedLabel>
              <InputSecurePassword id="NewSecurePassword" onChange={(event) => {EmptyField(event)}} />
              <RedLabel id="LNewSecurePassword" className="isActive">Ingrese nuevamente su nueva contraseña</RedLabel>
              <SubmitButton value="Cambiar Contraseña" />
            </ContentForm>
          </ContentWrapper>
        </RecoverPasswordWrapper>
      </RecoverPasswordContainer>

    )
  }

}

export default RecoverPasswordSection;
