import styled from 'styled-components';

export const SectionInfoContainer = styled.div`
  margin-top:0px;
  min-height:800px;  
  width:100%;
`;
export const SectionWrapper = styled.div` 
  display:flex;
  padding:50px 50px;
  flex-direction:column;
  flex-wrap:nowrap;
  justify-content:flex-start;
`;
export const SectionTitle = styled.h1`
  padding:20px 0px;
  text-align:center;
  color:var(--GreenStrong);
  font-family:var(--Header-Family)
  font-size: 40px;
`;

export const ContentWrapper = styled.div`

`;
export const SectionP = styled.p`
  padding:10px 0px;
  color:#797d83;
  font-size: 18px;
  text-align:justify;
`;
export const SectionSubtitle = styled.p`
  text-align:justify;
  padding:10px 0px;
  color:#797d83;
  font-size: 20px;
  font-weight:bold;
`;
