import styled from 'styled-components';
import {FaMapMarkerAlt, FaUser, FaCalendarAlt} from 'react-icons/fa'

export const BookingbarContainer = styled.div`
  display:${({activateBookingBar}) => (activateBookingBar ? 'block' : 'none')};
  position:absolute;
  bottom:100px;
  background:var(--White);
  z-index:30;
  width:85%;
  height:60px;
  margin:0 100px;
  transition: all 0.2s ease-in-out;
  
  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
    bottom:60px;
    height:150px;
  }

  @media screen and (max-width:480px){
    transition: all 0.2s ease-in-out;
    bottom:40px;
    height:150px;
  }
  

`;
export const BookingbarWrapper = styled.form`
  display:grid;
  align-items:center;
  justify-content:center;
  grid-template-columns:1fr 1fr 1fr 0.4fr 1fr;
  grid-template-areas: "d d1 d2 qp bt"; 
  height:100%;
  width:100%;
  transition: all 0.2s ease-in-out;
  
  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
    grid-template-columns:1fr 1fr;
    grid-template-rows:1fr 1fr 1fr;
    grid-template-areas: "d d" "d1 d2" "qp bt"; 
  }
`;

export const Column1 = styled.div`
  width:100%;
  height:100%;
  display:flex;
  justify-content:center;
  align-items:center;
  padding: 10px 10px 0 15px;
  text-align:left;
  grid-area:d;
`;


export const Column2 = styled.div`
  width:100%;
  height:100%;
  display:flex;
  justify-content:center;
  align-items:center;
  padding: 10px 10px 0 15px;
  grid-area:d1;
`;
export const Column3 = styled.div`
  width:100%;
  height:100%;
  display:flex;
  justify-content:center;
  align-items:center;
  padding: 10px 10px 0 15px;
  grid-area:d2;
`;
export const Column4 = styled.div`
  width:100%;
  height:100%;
  display:flex;
  justify-content:center;
  align-items:center;
  padding: 10px 10px 0 15px;
  grid-area:qp;
`;
export const Column5 = styled.div`
  width:100%;
  height:100%;
  display:flex-start;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  grid-area:bt;
`;

export const TopLineSelection = styled.div` 
  @media screen and (min-width: 768px){
    display:none;
    position:absolute;
    top:0;
    left:calc((${props => props.step} - 1)*(100%/4.4));
    background:var(--GreenLight);
    height:10px;
    width:calc((${props => props.lenght}*100%)/4.4);
    
    &.isActive{
      display:block;
    }
  }

`

export const Destination = styled.p`
  color:#797d83;
  font-size: 18px;
`
export const IconMap = styled(FaMapMarkerAlt)`
  margin-right:10px;
  color:#797d83;
  font-size: 20px;
`
export const IconDate = styled(FaCalendarAlt)`
  margin-right:10px;
  color:#797d83;
  font-size: 20px;
`
export const IconUser = styled(FaUser)`
  margin-right:10px;
  color:#797d83;
  font-size: 20px;
`

export const InputCheckInDate = styled.input`
  width:100%;
  height:100%;
  font-size: 18px;
  border:none;
  cursor:pointer;
  color:#797d83;
  outline:none;
  transition:all 150ms ease-in-out;
  @media screen and (max-width:480px){
    font-size: 14px;
  }
`
export const InputCheckOutDate = styled.input`
  width:100%;
  height:100%;
  font-size: 18px;
  border:none;
  cursor:pointer;
  color:#797d83;
  outline:none;
  transition:all 150ms ease-in-out;

  @media screen and (max-width:480px){
    font-size: 14px;
  }
`
export const Guests = styled.input`
  width:100%;
  height:100%;
  font-size: 18px;
  border:none;
  cursor:pointer;
  color:#797d83;
  outline:none;
  transition:all 150ms ease-in-out;

  @media screen and (max-width:480px){
    font-size: 14px;
  }
`
export const Booking = styled.input.attrs({type: 'submit', value: 'Reservar'})`
  width:100%;
  height:100%;
  background:var(--GreenLight);
  color:var(--White);
  font-weight:bold;
  font-size: 18px;
  border:none;
  cursor:pointer;
  transition:all 300ms ease-in-out;

  &:hover{
    border:2px solid #E7FEFD;
    transition:all 300ms ease-in-out;
    color:var(--White);
    background:var(--GreenStrong);
  }
`;
