import styled from 'styled-components'
import {Link as LinkR} from 'react-router-dom'
import {Link as LinkScroll} from 'react-scroll'
import {AiOutlineUser} from 'react-icons/ai';

export const Nav = styled.nav`
  //background: ${({scrollnav}) => (scrollnav ? 'linear-gradient(315deg, #e01c34 0%, #01BF71 74%)' : 'transparent')};
  background:transparent;
  height:120px;
  /*margin-top:-80px;*/
  display:${({section}) => (section ? 'none' : 'flex')};
  justify-content:center;
  align-items:center;
  font-size:0.8rem;
  position:sticky;
  top:0;
  z-index: 10;
  transition 0.8s all ease;

  @media screen and ( max-width: 960px){
    transition 0.8s all ease;
    height:80px;
  }
`


export const NavbarContainer = styled.div`
  display:flex;
  justify-content:space-between;
  height:80px;
  z-index:1;
  width:100%;
  padding:0 24px;
  max-width: 1100px;
`

export const NavLogo = styled(LinkR)`
  z-index:50;
  color: #fff;
  display: flex;
  justify-content: flex-start;
  align-items:center;
  cursor:pointer; 
  margin:${({scrollnav}) => (scrollnav ? '0 25px' : '60px 25px')};
  //margin: 0 25px;
  transition 0.8s all ease;
  
  @media screen and ( max-width: 960px){
    transition 0.8s all ease;
    margin: 0 10px;
  }

  @media screen and ( max-width: 480px){
    margin-top:2vh;
  }
`
export const BackgroundLogo = styled.div`
  height:150px;
  width:150px;
  background-color:var(--White);
  border-radius:50%;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

  @media screen and ( max-width: 960px){
    transition 0.8s all ease;
    height:100px;
    width:100px;
  }


`
export const Logo = styled.img` 
  position:relative;
  left:20px;
  top:10px;
  height:110px;
  width:110px;
  transition 0.8s all ease;

  @media screen and ( max-width: 960px){
    transition 0.8s all ease;
    top:15px;
    left:21px;
    height:60px;
    width:60px;
  }

`
export const MobileIcon = styled.div`
  display:none;

  @media screen and (max-width:768px){
    display:block;
    position:absolute;
    top: 0;
    right: 0;
    transform: translate(-100%,60%);
    font-size:1.8rem;
    cursor: pointer;
    color:var(--White);
  }
`

export const NavMenu = styled.ul`
  display:flex;
  align-items:center;
  list-style:none;
  text-align: center;
  margin-right: -22px;
  padding-bottom:40px;

  @media screen and (max-width: 768px){
  display:none;
  }

`
export const NavItem = styled.li`
  height:80px;
  
`
export const NavLinks = styled(LinkScroll)`
  color:${({scrollnav}) => (scrollnav ? 'var(--White)' : 'var(--PinkLight)')};
  display:flex;
  align-items:center;
  font-weight:bold;
  text-decoration:none;
  padding:0 1rem;
  height: 100%;
  cursor: pointer;

  &.active{
    border-bottom: 3px solid var(--PinkLight);
  }
  
`
export const BottomLine = styled.div`
  display:${({scrollnav}) => (scrollnav ? 'block' : 'none')};
  position:absolute;
  background:var(--White);
  height:1px;
  width:calc(100%/1.5);
  right:calc(100%/50);
  top:60%;
  transition 0.8s all ease;

  @media screen and ( max-width: 960px){
    transition 0.8s all ease;
    right:calc(100%/50);
    width:calc(100%/1.5);
    top:65%;
  }

  @media screen and (max-width: 768px){
    display:none;
  }
`


export const NavSection = styled.nav`
  background:var(--GreenLight);
  height:60px;
  display:${({section}) => (section ? 'flex' : 'none')};
  justify-content:center;
  align-items:center;
  font-size:0.8rem;
  position:sticky;
  top:0;
  z-index: 10;
  transition 0.8s all ease;

  @media screen and ( max-width: 960px){
    transition 0.8s all ease;
    height:80px;
  }
`
export const NavMenuSection = styled.ul`
  display:flex;
  align-items:center;
  list-style:none;
  text-align: center;

  @media screen and (max-width: 768px){
  display:none;
  }
`

export const NavButton = styled(LinkR)` 
  background:var(--GreenStrong);
  white-space:nowrap;
  padding:30px 80px;
  color:var(--White);
  font-size:20px;
  outline:none;
  cursor:pointer;
  width:100%;
  height:40px;
  display:flex;
  justify-content:center;
  align-items:center;
  text-decoration:none;
  transition: all 0.2s ease-in-out;

  &:hover{
    transition: all 0.2s ease-in-out;
    background:var(--White);
    color:var(--GreenStrong);
  }

  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
  }
`

export const NavLogoSection = styled(LinkR)`
  z-index:50;
  color: var(--White);
  display: flex;
  justify-content: flex-start;
  align-items:center;
  cursor:pointer; 
  margin:${({scrollnav}) => (scrollnav ? '60px 25px 0px 25px' : '90px 25px 0px 25px')};
  //margin: 0 25px;
  transition 0.8s all ease;
  
  @media screen and ( max-width: 960px){
    transition 0.8s all ease;
    margin: 0 10px;
  }
`

export const NavBtn = styled.li`
  display:flex;
  align-items:center;
  justify-content:center;
  width:10vw;

  @media screen and (max-width:768px){
    display:none;
  }
`

export const NavBtnLink = styled(LinkR)`
  border-radius:50px;
  white-space:nowrap;
  padding: 10px 22px;
  font-size:16px;
  outline:none;
  border:none;
  cursor:pointer;
  transition:all 0.2s ease-in-out;
  text-decoration:none;
  font-size:1rem;
  font-weight:bold;
  color:var(--PinkStrong);
  background:var(--White);
  &:hover{
    transition:all 0.2s ease-in-out;
    background:var(--White);
    color:var(--GreenLight);
  }
`

export const UserIcon = styled(AiOutlineUser)`
  font-size:3vh;
  color:var(--White);
`
export const NameIcon = styled.p`
  font-size:1.5vh;
  color:var(--White);
  padding-right:10px;
`

export const NavItemDropDownList = styled.div`
  display:flex;
  align-items:center;
  justify-content:flex-end;
  height: 5vh;
  width:10vw;
  z-index:30;
  transition:all 0.8s ease-in-out;

  &:hover{
    transition:all 0.8s ease-in-out;
  }
`

export const DropItem = styled.div`
  display:none;
  position:absolute;
  top:5vh;
  left:0vw;
  width:10vw;
  height:100%;
  transition:all 0.8s ease-in-out;

  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1000;
  &:hover {
    transition:all 0.8s ease-in-out;
  }
`

export const DropDown = styled.div`
  position:relative;
  width:10vw;
  height:100%;
  display:flex;
  align-items:center;
  justify-content:center;
  transition: all 0.8s ease-in-out;

  &:hover ${DropItem}{
    display:block;
    transition: all 0.8s ease-in-out;
  }
`
export const NavLinksDropDownList = styled(LinkR)`
  display:flex;
  align-items:center;
  font-size:0.8rem;
  font-weight:bold;
  text-decoration:none;
  height: 100%;
  width:10vw;
  cursor: pointer;
  padding: 12px 16px;
  z-index:30;
  color:var(--White);
  background-color:var(--GreenLight);

  &.active{
    border-bottom: 3px solid #16355a;
  }
  &:hover{
    transition: all 0.8s ease-in-out;
    background:var(--White);
    color:var(--GreenStrong);
  }

  @media screen and ( max-width: 960px){
    transition 0.8s all ease;
    font-size:0.6rem;
  }
  
`

export const NavLinksDropDownListDiv = styled.div`
  display:flex;
  align-items:center;
  font-size:0.8rem;
  font-weight:bold;
  text-decoration:none;
  height: 100%;
  width:10vw;
  cursor: pointer;
  padding: 12px 16px;
  z-index:30;
  color:var(--White);
  background-color:var(--GreenLight);

  &.active{
    border-bottom: 3px solid #16355a;
  }
  &:hover{
    transition: all 0.8s ease-in-out;
    background:var(--White);
    color:var(--GreenStrong);
  }
  @media screen and ( max-width: 960px){
    transition 0.8s all ease;
    font-size:0.6rem;
  }
  
`

