import React, {useState, useEffect} from 'react';
import Navbar from '../components/Navbar';
import HeroSection from '../components/HeroSection';
import Band from '../components/Band';
import Footer from '../components/Footer';
import ExperienceSectionInfo from '../components/ExperienceSectionInfo/index';
import LoadingPage from '../components/LoadingPage';
import SidebarSection from '../components/SideBar';


const ExperiencePage = ({cart, setPropCart, AvailableTents, setAvailableTents, keyword, user, CloseSession}) => {
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const loadData = async () => {
      await new Promise((r) => setTimeout(r, 1200));
      setLoading((loading) => !loading);
    };
    loadData();
  }, [])

  const [isOpen, setIsOpen] = useState(false)

  const toggle = () => {
    setIsOpen(!isOpen)
  }
  if (loading) {
    return <LoadingPage />
  }

  else {
    return (
      <>
        <Band />
        <Navbar toggle={toggle} section={true} user={user} CloseSession={CloseSession} />
        <SidebarSection isOpen={isOpen} toggle={toggle} user={user} CloseSession={CloseSession} />
        <HeroSection section={'experience'} cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} user={user} />
        <ExperienceSectionInfo keyword={keyword} />
        <Footer />
      </>
    )

  }
}
export default ExperiencePage;
