import styled from 'styled-components'


export const InfoContainer = styled.div`
  color: #fff;
  margin:${props => props.marginTopBot};
  background: ${({lightBg}) => (lightBg ? 'var(--White)' : 'var(--GreenLight)')};

  @media screen and (max-width: 768px){
    padding: 100px 0;
  }
`;

export const InfoWrapper = styled.div`
  display:grid;
  z-index:1;
  height:auto;
  width: 100%;
  max-width:1100px;
  margin-right:auto;
  margin-left:auto;
  padding: 40px 24px;
  justify-content: center;

  @media screen and (max-width: 768px){
    padding: 0px 24px;
    height:auto;
  }
`;

export const InfoRow = styled.div`
  display:grid;
  grid-auto-columns:minmax(auto,1fr);
  align-items:center;
  grid-template-areas: ${({imgStart}) => (imgStart ? `'col2 col1'` : `'col1 col2'`)};
  @media screen and (max-width: 768px){
  grid-template-areas: ${({imgStart}) => (imgStart ? `'col1' 'col2'` : `'col1 col1' 'col2 col2'`)}
  }
`;

export const Column1 = styled.div`
  margin:15px 0;
  padding: 0 15px;
  grid-area:col1;
`;

export const Column2 = styled.div`
  margin-bottom:15px;
  padding: 0 15px;
  grid-area:col2;
`

export const TextWrapper = styled.div`
  max-width:540px;
  padding-top: 0;
  padding-bottom:60px;
`;

export const TopLine = styled.p`
  color: ${({darkText}) => (darkText ? 'var(--GreenStrong)' : 'var(--PinkLight)')};
  font-size:16px;
  line-height:16px;
  font-weight:700;
  letter-spacing:10px;
  text-transform:uppercase;
  margin-bottom:16px
`;

export const Heading = styled.h1`
  margin-bottom:24px;
  font-size:48px;
  line-height:1.1;
  font-weight:600;
  color:${({lightText}) => (lightText ? 'var(--GreenLight)' : 'var(--White)')};

  @media screen and (max-width: 480px){
    font-size:32px;
  }
`;

export const Subtitle = styled.p`
  max-width:440px;
  margin-bottom:35px;
  font-size:18px;
  line-height:24px;
  color: ${({darkText}) => (darkText ? 'var(--GreenStrong)' : 'var(--White)')};
`;

export const BtnWrap = styled.div`
  display:flex;
  justify-content: flex-start;
`;

export const ImgWrap = styled.div`
  max-width:555px;
  height:100%;
`;

export const Img = styled.img`
  width:100%;
  margin:0 0 10px 0;
  padding-right:0;
  border-radius: ${({imgCircle}) => (imgCircle ? '50%' : '1vh')};
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  background-position:center;
`;

export const TableWrap = styled.div`
  max-width:555px;
  height:100%;
`;
export const Table = styled.table`
  width:100%;
  height:100%;
  border:2px solid var(--White);
  background-color:var(--GreenLight);
  text-align:center;
`;
export const HeaderTable = styled.th`
  font-family:var(--Header-Family);
  border-bottom:2px solid var(--White);
`;
export const RowTable = styled.td` 
`;
export const Labelp = styled.p`
  color:var(--White);
  font-size:0.8rem;
`

export const Promotions = styled.div`
  border-top:1px solid var(--PinkLight);
  margin-top:1vh;
  padding-top:1vh;
`
export const HeaderPromotion = styled.h2`
  color: ${({darkText}) => (darkText ? 'var(--GreenStrong)' : 'var(--PinkLight)')};
  text-align: ${({orientation}) => (orientation ? 'left' : 'right')};
`
export const ListofBulltes = styled.ul`
  color:${({lightText}) => (lightText ? 'var(--GreenLight)' : 'var(--White)')};
  padding-left:1vw;
  direction: ${({orientation}) => (orientation ? 'lftr' : 'rtl')};
`
export const Bullets = styled.li`
  color:${({lightText}) => (lightText ? 'var(--GreenLight)' : 'var(--White)')};
`
export const PricePromotion = styled.h1`
  color: ${({darkText}) => (darkText ? 'var(--GreenStrong)' : 'var(--PinkLight)')};
  text-align: ${({orientation}) => (orientation ? 'right' : 'left')};
  &:before { content:"PEN"}
  &:after{content:".00"}
`

