import React, {useState, useEffect} from 'react';
import {useNavigate} from "react-router-dom";
import axios from 'axios';
import ImageBackground from '../../images/Background/background.png';
import NotificationBandSection from '../NotificationBand';
import {Container, FormWrap, Icon, FormContent, Form, FormH1, Formlabel, FormButton, FormInput, TextReference} from './RegisterElements';
import {PATH_PREFIX_BACKEND} from "../../Data/EnvironmentVariables"

const RegisterComponent = () => {

  let navigate = useNavigate();
  const [RegisterAPI, setRegisterAPI] = useState({tipo: 'POST', url: PATH_PREFIX_BACKEND + '/api/users/register'});
  const [errorAPI, setErrorAPI] = useState(null);
  const [respuestaAPI, setRespuestaAPI] = useState(null);
  const [AssertPassword, setAssertPassword] = useState(null);

  const sendData = async (e) => {
    e.preventDefault();
    if (document.getElementById('password').value !== document.getElementById('password2').value) {
      setAssertPassword("Las contraseñas no son iguales");
      return;
    }
    const datos = {
      email: document.getElementById('email').value,
      name: document.getElementById('name').value,
      password: document.getElementById('password').value,
      phonenumber: document.getElementById('phoneNumber').value,
      email2: document.getElementById('Email2').value
    }
    const TriggerRegister = async () => {
      setErrorAPI(null);
      try {
        await axios({method: RegisterAPI.tipo, url: RegisterAPI.url, data: datos}).then(response => {
          setRespuestaAPI(response.data)
        })
        await new Promise((r) => setTimeout(r, 4500));
        await navigate('/signin')
      } catch (error) {
        setErrorAPI(error.response.data)
      }
    };
    await TriggerRegister();
  }

  /*******************************NOTIFCATION VARIABLES************************************************/
  const [typeNotification, setTypeNotification] = useState("Success");
  const [MessageNotification, setMessageNotification] = useState("No Message");
  const [TriggerNotification, setTriggerNotification] = useState(false);

  const AppearNotification = async () => {
    await new Promise((r) => setTimeout(r, 4000));
    await setTriggerNotification(false);
  };

  useEffect(() => {
    if (errorAPI !== null) {
      setTypeNotification("Error");
      setMessageNotification(errorAPI.status);
      setTriggerNotification(true);
      AppearNotification();
      setErrorAPI(null);
    } else if (respuestaAPI !== null) {
      setTypeNotification("Success");
      setMessageNotification(respuestaAPI.status);
      setTriggerNotification(true);
      AppearNotification();
      setRespuestaAPI(null);
    } else if (AssertPassword !== null) {
      setTypeNotification("Error");
      setMessageNotification(AssertPassword);
      setTriggerNotification(true);
      AppearNotification();
      setAssertPassword(null);
    }
  }, [errorAPI, respuestaAPI, AssertPassword])

  return (
    <>
      <Container imgsrc={ImageBackground}>
        <NotificationBandSection type={typeNotification} message={MessageNotification} trigger={TriggerNotification} />
        <FormWrap onSubmit={(event) => {sendData(event)}}>
          <Icon to="/">Bambucamp</Icon>
          <FormContent>
            <Form action="#">
              <FormH1>Sé parte de Bambucamp</FormH1>
              <Formlabel gridarea="emailL" htmlFor='for'>Email</Formlabel>
              <FormInput gridarea="email" id='email' type='email' required />
              <Formlabel gridarea="nameL" htmlFor='for'>Nombres y Apellidos</Formlabel>
              <FormInput gridarea="name" id='name' type='text' required />
              <Formlabel gridarea="passwordL" htmlFor='for'>Nueva Contraseña</Formlabel>
              <FormInput gridarea="password" id='password' type='password' required />
              <Formlabel gridarea="newpasswordL" htmlFor='for'>Nueva Contraseña</Formlabel>
              <FormInput gridarea="newpassword" id='password2' type='password' required />
              <Formlabel gridarea="numberPhoneL" htmlFor='for'>Numero de Telefono</Formlabel>
              <FormInput gridarea="numberPhone" id='phoneNumber' type='text' required />
              <Formlabel gridarea="EmailSecL" htmlFor='for'>Email Secundario de Referencia</Formlabel>
              <FormInput gridarea="EmailSec" id='Email2' type='email' required />
              <FormButton gridarea="button" type='submit'>Registrarme</FormButton>
            </Form>
          </FormContent>
        </FormWrap>
      </Container>
    </>
  )
}


export default RegisterComponent;
