import React, {useState, useEffect} from 'react';
import {PopUpContainer, PopUpWrapper, PopUpHeader, PopUpDescription, PopUpCloseIcon} from './LogPopUpElements';

const LogPopUpComponent = ({header, message, Open, setOpen}) => {
  const ClosePopUp = () => {
    setOpen(false);
  }
  return (
    <PopUpContainer Open={Open}>
      <PopUpWrapper>
        <PopUpCloseIcon onClick={ClosePopUp} />
        <PopUpHeader>
          {header}
        </PopUpHeader>
        <PopUpDescription>
          {message}
        </PopUpDescription>
      </PopUpWrapper>
    </PopUpContainer>
  );
};
export default LogPopUpComponent;
