
import React, {useState, useEffect} from 'react'
import {WarningWrapper, ContentWrapper, WarningContainer, WrapperButtons, Title, Message, InputReasons, LabelRed, ButtonItem} from './WarningWindowElements';
import {ButtonCartForward} from '../ButtonElements';
import {animateScroll as scroll} from 'react-scroll';

const WarningSection = ({data, TriggerFunction, type}) => {

  const [activeMessage, setActiveMessage] = useState(false);
  useEffect(() => {
    setActiveMessage(data.status);
    const toggleHome = () => {
      scroll.scrollToTop();
    };
    toggleHome();
  }, [data]);

  const [disabledButton, setDisabledButton] = useState(true);
  const [Parameters, setParameters] = useState(null);

  const closeMessage = () => {
    setActiveMessage(false);
  }

  const EmptyField = (event) => {
    var id = event.target.id;
    var element = document.getElementById(id);
    var label = document.getElementById(`L${id}`);
    if (element.value === "") {
      label.classList.remove("isActive");
      label.classList.add("isActive");
    } else {
      label.classList.remove("isActive");
      setDisabledButton(false);
    }
    setParameters(element.value)
  }
  return (
    <WarningContainer activeSection={activeMessage}>
      <WarningWrapper>
        <ContentWrapper>
          <Title>{data.title}</Title>
          <Message>{data.message}</Message>
          {type === "Request" && (
            <>
              <Message>Escribe las razones de tu cancelación</Message>
              <InputReasons id="Reasons" onChange={(event) => {EmptyField(event)}} />
              <LabelRed id="LReasons" className="isActive">Llena la razon de tu cancelación</LabelRed>
              <WrapperButtons>
                <ButtonItem>
                  <ButtonCartForward id="cancel" onClick={closeMessage}>Cancelar</ButtonCartForward>
                </ButtonItem>
                <ButtonItem>
                  <ButtonCartForward id="nextStep" disabled={disabledButton} onClick={() => TriggerFunction(Parameters)}>Continuar</ButtonCartForward>
                </ButtonItem>
              </WrapperButtons>
            </>
          )}
          {type === "Warning" && (
            <>
              <WrapperButtons>
                <ButtonCartForward id="cancel" onClick={closeMessage}>Cancelar</ButtonCartForward>
                <ButtonCartForward id="nextStep" onClick={TriggerFunction}>Continuar</ButtonCartForward>
              </WrapperButtons>
            </>
          )}
        </ContentWrapper>
      </WarningWrapper>
    </WarningContainer>
  )
}

export default WarningSection;
