import React, {useState, useEffect} from 'react';
import LoadingPage from '../components/LoadingPage';
import NavCart from '../components/CartSection/NavCart';
import HeroSection from '../components/HeroSection';
import Footer from '../components/Footer';
import {CartFourthSectionPage} from '../components/CartSection/CartFourthSection';
import axios from 'axios';

const ConfirmationSection = ({token, user, cart, setPropCart}) => {

  const [loading, setLoading] = useState(false);
  const [accessoAPI, setAccesoAPI] = useState({tipo: 'GET', url: 'http://localhost:3000/api/tents'});
  const [datos, setDatos] = useState([])
  const [errorAPI, setErrorAPI] = useState(null);
  const [respuestaAPI, setRespuestaAPI] = useState({respuesta: 'KO'});

  useEffect(() => {
    const consultaAPI = async () => {
      setErrorAPI(null);
      try {
        const consulta = await axios({method: accessoAPI.tipo, url: accessoAPI.url, data: datos})
        setRespuestaAPI(consulta.data);
      } catch (error) {
        setErrorAPI(error.response)
      }
      setLoading(true);
    };
    consultaAPI();
  }, [accessoAPI.tipo, accessoAPI.url, datos])

  if (loading === false) {
    return <LoadingPage />
  } else {
    return (
      <>
        <NavCart section={true} step={4} />
        <HeroSection section={'cart-4'} cart={cart} setPropCart={setPropCart} />
        <CartFourthSectionPage token={token} user={user} cart={cart} setPropCart={setPropCart} />
        <Footer />
      </>
    )

  }
}

export default ConfirmationSection;
