import React, {useState, useEffect} from 'react'
import Video from '../../images/Banner/Banner.mp4';
import ImageLogo from '../../images/Banner/Logo3.png';
import Background from '../../images/Banner/BambucampHero.jpeg'
import {ButtonR} from '../ButtonElements';
import {FaRegClock, FaTag, FaBus, FaPercentage} from 'react-icons/fa';
import Bookingbar from '../Bookingbar'
import {HeroContainer, HeroH1, HeroP, HeroContent, VideoHero, ArrowForward, ArrowRight, HeroBand, HeroBandP, HeroBandWrapper, Column1, Column3, Column2, Column2Wrapper, Col2HeroBandP, HeroIcon, LogoHero, HeroBg} from './HeroElements';

const HeroSection = ({section, cart, setPropCart, AvailableTents, setAvailableTents, user}) => {
  const [hover, setHover] = useState(false)
  const [marginTop, setMarginTop] = useState('-120px')
  const [heightHero, setHeightHero] = useState("")
  const [header, setHeader] = useState(true)
  const [subheader, setSubHeader] = useState("Encuentrate con la naturaleza y escapate de la rutina")
  const [activateBookingBar, setActivateBookingBar] = useState(true)
  const [activateHeroBand, setActivateHeroBand] = useState(true)
  const [HeightMobile, setHeightMobile] = useState('60vh');

  useEffect(() => {
    if (section === 'cart-1') {
      setHeightHero('50vh')
      setMarginTop('-120px')
      setActivateBookingBar(true)
      setActivateHeroBand(true)
      setHeader(false)
      setSubHeader("Escoge tu Tienda...")
      setHeightMobile('70vh');
    } else if (section === 'cart-2' || section === 'cart-3') {
      setHeightHero('50vh')
      setMarginTop('-120px')
      setActivateBookingBar(false)
      setActivateHeroBand(false)
      setHeader(false)
      setSubHeader("Ya casi terminamos...")
    } else if (section === 'cart-4') {
      setHeightHero('25vh')
      setMarginTop('-120px')
      setActivateBookingBar(false)
      setActivateHeroBand(false)
      setHeader(false)
      setSubHeader(" ")
    } else if (section === 'index') {
      setHeightHero('75vh')
      setActivateBookingBar(true)
      setActivateHeroBand(true)
      setHeader(true)
      setHeightMobile('93vh');
    } else if (section === 'experience') {
      setMarginTop('-60px')
      setHeightHero('60vh')
      setActivateBookingBar(true)
      setActivateHeroBand(true)
      setHeader(false)
      setSubHeader("Encuentra la experiencia que se adecue a ti")
    }
    else {
      setMarginTop('0')
      setHeightHero('40vh')
      setHeader(false)
      setActivateBookingBar(false)
      setActivateHeroBand(false)
      setSubHeader("Animate a vivir nuevas experiencias")
    }
  }, [section])
  const onHover = () => {
    setHover(!hover)
  }


  return (
    <HeroContainer id="home" section={heightHero} marginTop={marginTop} HeightMobile={HeightMobile}>
      {section === 'index' && (
        <VideoHero autoPlay loop muted src={Video} type='video/mp4' />
      )}
      {section !== 'index' && (
        <HeroBg imgsrc={Background} />
      )}
      <HeroContent>
        {/*section === 'index' && (
          <LogoHero imgsrc={ImageLogo}></LogoHero>
        )*/}
        {section !== 'index' && (
          <>
            <HeroH1>{header}</HeroH1>
            <HeroP>{subheader}</HeroP>
          </>
        )}
      </HeroContent>
      <Bookingbar activateBookingBar={activateBookingBar} cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} />
      <HeroBand activateHeroBand={activateHeroBand}>
        <HeroBandWrapper>
          <Column1>
            <HeroBandP>Accede para tener mas beneficios</HeroBandP>
          </Column1>
          <Column2>
            <Column2Wrapper>
              <HeroIcon>
                <FaRegClock />
              </HeroIcon>
              <Col2HeroBandP>Ahorra tiempo en tu compra</Col2HeroBandP>
            </Column2Wrapper>
            <Column2Wrapper>
              <HeroIcon>
                <FaTag />
              </HeroIcon>
              <Col2HeroBandP>Enterate de Promociones de Temporada</Col2HeroBandP>
            </Column2Wrapper>
            <Column2Wrapper>
              <HeroIcon>
                <FaBus />
              </HeroIcon>
              <Col2HeroBandP>Agiliza tu check-in</Col2HeroBandP>
            </Column2Wrapper>
            <Column2Wrapper>
              <HeroIcon>
                <FaPercentage />
              </HeroIcon>
              <Col2HeroBandP>Obten Descuentos</Col2HeroBandP>
            </Column2Wrapper>
          </Column2>
          <Column3>
            {user !== null ?
              <></>
              :
              <ButtonR to="/signin" onMouseEnter={onHover} onMouseLeave={onHover} gridarea='bt' big='true'>Acceder{hover ? <ArrowForward /> : <ArrowRight />}</ButtonR>
            }
          </Column3>
        </HeroBandWrapper>
      </HeroBand>
    </HeroContainer >
  );
};


export default HeroSection;
