import styled from 'styled-components';
import {Link as LinkR} from 'react-router-dom'


export const ExperienceContainer = styled.div`
  width:100%; 
  height:auto;
  background:var(--White);
  display:flex;
  padding: 120px 140px 60px 140px;
  flex-direction:column;
  justy-content:center;
  align-items:center;
  transition: all 0.2s ease-in-out;

  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
    padding:40px 20px; 
    height:auto;
  }
`;

export const ExperienceWrapper = styled.div`
  display: grid;
  align-items: center;
  margin:40px;
  justify-content: center;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows:1fr 1fr;
  grid-template-areas: "f f a a" "d b en bo";
  height: 600px;
  width: 100%;
  transition: all 0.2s ease-in-out;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

  @media screen and (max-width: 768px){
    display:none;
  }
`;
export const ExperienceHeader = styled.h1`
  text-align:center;
  color:var(--GreenLight);
  font-family:var(--Header-Family);
  font-size: 40px;
`;
export const ExperienceSubtitle = styled.p`
  text-align:center;
  color:var(--GreenStrong);
  font-family:var(--Text-Family-Secondary);
  font-size: 18px;
`;
export const GridElement = styled.div`
  width:100%;
  height:100%;
  background-image: url(${props => props.imgsrc});
  background-size:cover;
  background-position:center;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
  grid-area:${props => props.gridarea};
`;

export const ExperienceLink = styled(LinkR)`
  width:100%;
  height:100%;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
  text-align:center;
  text-decoration:none; 

  @media screen and (max-width: 768px){
    align-items:flex-start;

  }
`;
export const Title = styled.h1`
  color:#fff;
  font-family:var(--Header-Family);
  font-style:italic;
  font-size: 18px;
`;
export const Header = styled.h1`
  color:var(--White);
  font-size: 18px;
`;
export const Subtitle = styled.p`
  color:#fff;
  font-size: 16px;
  &::before{
    content:'-'
  }
  &::after{
    content:'-'
  }
`;


export const ExperienceWrapperMobileView = styled.div`
  display:none;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  width:100%;
  transition: all 0.2s ease-in-out;
  @media screen and (max-width: 768px){

    transition: all 0.2s ease-in-out;
    display:block;
  }
`;

export const ExperienceElement = styled.div`
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  box-sizing:border-box;
  width:100%;
  height:100px;
  margin:30px 0;
  padding:2px 0px;

`;
export const TitleMobileView = styled.h1`
  font-size:20px;
  color:var(--White);
  font-family:var(--Header-Family);
`;

