import styled from 'styled-components';
import {FaTimes} from 'react-icons/fa';

export const ProfileContainer = styled.div`
  position:relative;
  min-height:100vh;
  width:100%;
`;
export const ProfileWrapper = styled.div`
  min-height:90vh;
  background-color:var(--White);
  width:100%;
  display:grid;
  grid-template-columns:0.25fr 0.75fr;
  grid-template-rows:1fr;
  grid-template-areas:"user" "Information";


  @media screen and (max-width:480px){
    grid-template-columns:1fr;
    grid-template-rows:auto auto;
    grid-template-areas:"user""content";
  }
`;
export const FirstColumn = styled.div`
  gird-area:user;
  width:100%;
  height:100%;
  padding:10vh 5vh 0 5vh;
  background-color:var(--GreenStrong);
  display:flex;
  flex-direction:column;
  justify-content:flex-start;
  align-items:center;
  @media screen and (max-width:480px){
    padding:5vh 5vh 0 5vh;
  }
`;

export const ImageProfileWrapper = styled.div`
  width:20vh; 
  height:20vh;
  border:5px solid var(--White);
  background:linear-gradient(0deg,rgba(236,220,204,0.4),rgba(0,0,0,0.7)), url(${props => props.imgsrc});
  background-size:cover;
  background-position:center;
  background-repeat:no-repeat;
  border-radius:50%;
  margin:2.5vh 0;

  @media screen and (max-width:480px){
  width:20vh; 
  height:20vh;
  }
`;

export const NameProfile = styled.div`
  font-size:3vh;
  color:var(--White);
  font-weight:bold;
  margin:2.5vh 0;
`;

export const DescriptionProfile = styled.div`
  font-size:1.5vh;
  color:var(--White);
  text-align:center;


`;
export const SecondColumn = styled.form`
  gird-area:content;
  width:100%;
  height:100%;
  display:grid;
  grid-template-columns:${({consultantView}) => (consultantView ? '0.5fr 0.5fr' : '1fr')};
  grid-template-rows:1fr auto;
  grid-template-areas:${({consultantView}) => (consultantView ? `"userInformation ConsultantInformation" "button button"` : `"userInformation" "button"`)};

  @media screen and (max-width:480px){
  grid-template-columns:auto;
  grid-template-rows:${({consultantView}) => (consultantView ? 'auto auto auto' : 'auto auto')};
  grid-template-areas:${({consultantView}) => (consultantView ? `"userInformation" "ConsultantInformation" "button"` : `"userInformation" "button"`)};
  }
`;

export const UserProfileSection = styled.div`
  grid-area:userInformation;
  width:100%;
  height:100%;
  display:flex;
  flex-direction:column;
  align-items:flex-start;
  justify-content:flex-start;
  padding-left:5vw;
  padding-top:5vh;

  @media screen and (max-width:480px){
    width:100%;
    height:100%;
    padding:5vh 10vw;
    align-items:center;
    justify-content:center;
  }
`;
export const ConsultantProfileSection = styled.div`
  grid-area:ConsultantInformation;
  width:100%;
  height:100%;
  display:${({consultantView}) => (consultantView ? 'flex' : 'none')};
  flex-direction:column;
  align-items:flex-start;
  justify-content:flex-start;
  padding:5vh 5vw 0 5vw;
  padding-top:5vh;

  @media screen and (max-width:480px){
    width:100%;
    height:100%;
    padding:5vh 10vw;
    align-items:center;
    justify-content:center;
  }
`;


export const InputName = styled.input.attrs({type: 'text', placeholder: 'Nombres'})`
  max-width:400px; 
  width:100%;
  height:3vw;
  text-decoration:none;
  outline:none;
  border-radius:1vh;
  border:1px solid var(--GreenStrong);
  color:var(--GreenStrong);
  padding:5px 10px;

  @media screen and (max-width:480px){
    height:5vh;
  }
`;

