export const AddSkill = styled.div`
  width:25%;
  height:3vh;
  border-radius:1vh;
  border:1px solid var(--DarkBlue);
  color:var(--DarkBlue);
  background:var(--Pink);
  padding:5px 5px;
  font-size:1vh;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
  cursor:pointer;
  transition:all 0.4s ease-in-out;
  @media screen and (max-width:480px){
    height:5vh;
  }
  &:hover {
    color:var(--Pink);
    background:var(--DarkBlue);
    transition:all 0.4s ease-in-out;
  }
`
export const RowButton = styled.div`
  grid-area:button;
  width:80%;
  height:auto;
  display:flex;
  flex-direction:column;
  align-items:flex-end;
  justify-content:center;
  margin:2.5vh 0;

  @media screen and (max-width:480px){
    align-items:center;
    width:100%;
    margin:2.5vh 0;
  }
`
export const InputProfileImage = styled.input.attrs({type: 'file'})`
  width:50%;
  min-height:3vh;
  height:auto;
  text-decoration:none;
  outline:none;
  border-radius:1vh;
  border:1px solid var(--DarkBlue);
  color:var(--DarkBlue);
  padding:5px 5px;
  font-size:1vh;
  @media screen and (max-width:480px){
    height:5vh;
  }
`;

export const SubmitButton = styled.input.attrs({type: 'submit'})`
  border-radius: 15px;
  background: ${({primary}) => (primary ? 'var(--White)' : 'var(--White)')};
  white-space:nowrap;
  padding:${({big}) => (big ? '14px 48px' : '12px 30px')};
  color:${({dark}) => (dark ? 'var(--GreenStrong)' : 'var(--GreenStrong)')};
  font-size:${({fontBig}) => (fontBig ? '20px' : '16px')};
  outline:none;
  cursor:pointer;
  display:flex;
  justify-content:center;
  align-items:center;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  transition: all 0.2s ease-in-out;

  &:hover{
    transition: all 0.2s ease-in-out;
    background: ${({primary}) => (primary ? 'var(--GreenStrong)' : 'var(--GreenStrong)')};
    color: ${({primary}) => (primary ? 'var(--White)' : 'var(--White)')};
  }

  @media screen and (max-width:480px){
    padding:${({big}) => (big ? '7px 24px' : '6px 15px')};
    font-size:${({fontBig}) => (fontBig ? '16px' : '14px')};
  }
`;

