import styled from 'styled-components'
import {Link as LinkR} from 'react-router-dom'


export const NavbarContainer = styled.div`
  display:flex;
  height:120px;
  z-index:1;
  width:100%;
  padding:0 24px;
  max-width: 1400px;
`

export const NavSection = styled.nav`
  background:transparent;
  height:120px;
  display:flex;
  justify-content:center;
  align-items:center;
  font-size:0.8rem;
  position:relative;
  top:0;
  z-index: 10;
  transition 0.8s all ease;

  @media screen and ( max-width: 960px){
    transition 0.8s all ease;
    height:80px;
  }
`
export const NavMenuSection = styled.div`
  display:flex;
  align-items:center;
  list-style:none;
  text-align: center;
  width:800px;

  @media screen and (max-width: 768px){
  display:none;
  }
`

export const NavLogoSection = styled(LinkR)`
  z-index:50;
  color: #fff;
  display: flex;
  justify-content: flex-start;
  align-items:center;
  cursor:pointer; 
  margin:${({scrollnav}) => (scrollnav ? '60px 25px 0px 25px' : '90px 25px 0px 25px')};
  transition 0.8s all ease;
  
  @media screen and ( max-width: 960px){
    transition 0.8s all ease;
    margin: 0 10px;
  }

  @media screen and ( max-width: 480px){
    margin-top:4vh;
  }
`
export const BackgroundLogo = styled.div`
  height:150px;
  width:150px;
  background-color:var(--White);
  border-radius:50%;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

  @media screen and ( max-width: 960px){
    transition 0.8s all ease;
    height:100px;
    width:100px;
  }
`

export const Logo = styled.img` 
  position:relative;
  left:20px;
  top:10px;
  height:110px;
  width:110px;
  transition 0.8s all ease;

  @media screen and ( max-width: 960px){
    transition 0.8s all ease;
    top:15px;
    left:21px;
    height:60px;
    width:60px;
  }
`
