import styled from 'styled-components'
import {MdKeyboardArrowRight, MdArrowForward} from 'react-icons/md'

export const HeroContainer = styled.div`
  background:#0c0c0c;
  display:flex;
  justify-content:center;
  align-items:center;
  padding: 0 30px;
  margin-top:${props => props.marginTop};

  height: ${props => props.section};
  position: relative;
  z-index:1;

  :before{
    content:'';
    position:absolute;
    right:0;
    bottom:0;
    background:linear-gradient(180deg,rgba(0,0,0,0.2) 0%,rgba(0,0,0,0.6) 100%), linear-gradient(180deg,rgba(0,0,0,0.2) 0%, transparent 100%);
    z-index:2;
  }

  @media screen and (max-width: 796px){
    height:93vh;
  }

  @media screen and (max-width: 480px){
    height:${props => props.HeightMobile};
  }
`

export const VideoHero = styled.video`
  position:absolute;
  top:0;
  left:0;
  overflow:hidden;
  width:100%;
  height:100%;
  object-fit:cover;
  -o-object-fit:cover;
  -object-fit:cover;
  background: #232a34;
`
export const HeroBg = styled.div`
  position:absolute;
  background-image:linear-gradient(rgba(0, 0, 0, 0.6),rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.1)) ,url(${props => props.imgsrc});
  background-size:cover;
  background-position:center;
  top:0;
  right:0;
  bottom:0;
  left:0;
  width:100%;
  height:100%;
  overflow:hidden;
`

export const ImgHero = styled.img`
  width:100%;
  height:100%;
  object-fit:cover;
  filter: blur(4px);
  -o-object-fit:cover;
  -object-fit:cover;
`
export const HeroContent = styled.div`
  z-index:3;
  max-width:1200px;
  position:absolute;
  padding: 8px 24px;
  display:flex;
  flex-direction:column;
  align-items:center
`

export const HeroH1 = styled.h1`  
  font-family:var(--Text-Family-Primary);
  font-style:italic;
  color: #fff;
  font-size: 70px;
  text-align:center;
  display:${({header}) => (header ? 'block' : 'none')};

  @media screen and (max-width: 1000px){
    font-size:50px;
  }
  @media screen and (max-width: 768px){
    font-size:40px;
  }

  @media screen and (max-width:480px){
    font-size:32px;
  }
`

export const HeroP = styled.p`
  font-family:var(--Text-Family-Primary);
  margin-top:24px;
  color: #fff;
  font-size: 40px;
  text-align:center;
  max-width:1000px;

  @media screen and (max-width: 1000px){
    font-size:30px;
  }
  @media screen and (max-width: 800px){
    font-size:24px;
  }

  @media screen and (max-width:480px){
    font-size:18px;
  }
`

export const HeroBtnWrapper = styled.div`
  margin-top: 32px;
  display:flex;
  flex-direction:column;
  align-items: center;
`

export const ArrowForward = styled(MdArrowForward)`
  margin-left:8px;
  font-size: 20px;
`

export const ArrowRight = styled(MdKeyboardArrowRight)`
  margin-left:8px;
  font-size: 20px;
`;
export const HeroBand = styled.div`
  display:${({activateHeroBand}) => (activateHeroBand ? 'block' : 'none')};
  position:absolute;
  z-index:20;
  background:var(--GreenLight);
  bottom:-70px;
  height:80px;
  width:100%;

`;

export const HeroBandWrapper = styled.div`
  display:grid;
  grid-template-columns:1fr 2fr 1fr;
  grid-template-areas: "p b bt"; 
  align-items:center;
  justify-content:center;
  height:100%;
  width:100%;
  
  @media screen and (max-width: 1200px){
    grid-template-columns:1fr 1fr;
    grid-template-areas: "p bt"; 
  }
  
  @media screen and (max-width: 480px){
    grid-template-columns:1fr;
    grid-template-areas: "bt"; 
  }
`;
export const Column1 = styled.div`
  display:flex-start;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  text-align:center;
  padding:0 100px;
  grid-area:p;
  
  @media screen and (max-width: 768px){
    padding:0 40px;
  }
  @media screen and (max-width: 480px){
    display:none;
  }
`;

export const Column2 = styled.div`
  display:grid;
  grid-template-columns:1fr 1fr 1fr 1fr;
  align-items:center;
  justify-content:center;
  height:100%;
  width:100%;
  padding:0 10px;
  color:var(--White);
  grid-area:b; 

  @media screen and (max-width: 1200px){
    display:none;
  }
`;
export const Column3 = styled.div`
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  padding:0 10px;
  grid-area:bt; 
`;
export const HeroBandP = styled.p`
  font-family: var(--Header-Family);
  font-size:20px;
  color:var(--White);
  font-weight:bold;
  @media screen and (max-width: 768px){
    font-size:15px;
  }

`;

export const Column2Wrapper = styled.div`
  display:flex;
  flex-direction:row;
  align-items:center;
  justify-content:center;
`
export const HeroIcon = styled.div`
  font-size:1.8rem;
`
export const Col2HeroBandP = styled.p`
  padding:0 20px;
  font-size:12px;
  text-transform: uppercase;
  text-align:left;
  color:var(--White);
`
export const LogoHero = styled.div`
  position: relative;
  background-image: url(${props => props.imgsrc});
  background-size:cover;
  top: -20px;
  right: 0;
  bottom: 0;
  left: 0;
  height: 230px;
  width:570px;

  @media screen and (max-width: 1000px){
    top: -40px;
  height: 230px;
  width:570px;
  }
  @media screen and (max-width: 768px){
    display:none;
    top: -40px;
    height: 230px;
    width:570px;
  }

  @media screen and (max-width:480px){
    display:none;
    top: -40px;
    height: 115px;
    width:285px;
  }
`
