import styled from 'styled-components';
import {Link} from 'react-scroll';
import {Link as LinkR} from 'react-router-dom'

export const Button = styled(Link)`
  background: ${({primary}) => (primary ? 'var(--White)' : 'var(--White)')};
  white-space:nowrap;
  padding:${({big}) => (big ? '14px 38px' : '12px 30px')};
  color:${({dark}) => (dark ? 'var(--GreenStrong)' : 'var(--GreenStrong)')};
  font-size:${({fontBig}) => (fontBig ? '20px' : '16px')};
  outline:none;
  border:2px solid var(--GreenStrong); 
  cursor:pointer;
  display:flex;
  width:250px;
  justify-content:center;
  align-items:center;
  transition: all 0.2s ease-in-out;

  &:hover{
    transition: all 0.2s ease-in-out;
    background: ${({primary}) => (primary ? 'var(--GreenStrong)' : 'var(--GreenStrong)')};
    color:${({dark}) => (dark ? 'var(--White)' : 'var(--White)')};
  }
  @media screen and (max-width: 723px){
    transition: all 0.2s ease-in-out;
    width:180px;
  }
  @media screen and (max-width: 480px){
    transition: all 0.2s ease-in-out;
    width:200px;
  }

`;

export const ButtonCartForward = styled.button`
  white-space:nowrap;
  padding:14px 72px;
  color:var(--GreenStrong);
  font-size:20px;
  outline:none;
  border:2px solid var(--GreenStrong);
  cursor:pointer;
  display:flex;
  justify-content:center;
  align-items:center;
  grid-area:col3;
  background: var(--White);
   &:hover{
      transition: all 0.2s ease-in-out;
      background: var(--GreenStrong);
      color:var(--White);



  @media screen and (max-width: 480px){
    padding:7px 36px;
    font-size:14px;
  }
`

export const ButtonCartForwardExtra = styled.button`
  white-space:nowrap;
  padding:14px 15px;
  color:var(--GreenStrong);
  font-size:20px;
  font-weight:800;
  outline:none;
  border:2px solid var(--GreenStrong);
  cursor:pointer;
  display:flex;
  justify-content:center;
  align-items:center;
  grid-area:col3;
  background: var(--White);
  z-index:1000;
   &:hover{
      transition: all 0.2s ease-in-out;
      background: var(--GreenStrong);
      color:var(--White);
  }

  @media screen and (max-width: 1200px){
    padding:7px 15px;
    font-size:12px;
    width:180px;
  }

  @media screen and (max-width: 480px){
    padding:7px 15px;
    font-size:10px;
    width:150px;
  }
`
export const ButtonExperience = styled(LinkR)`
  background:var(--GreenStrong);
  white-space:nowrap;
  padding:10px 3px;
  color:var(--White);
  font-size:20px;
  outline:none;
  border:2px solid var(--White); 
  cursor:pointer;
  width:80%;
  height:100%;

  display:flex;
  justify-content:center;
  align-items:center;
  text-decoration:none;
  transition: all 0.2s ease-in-out;

  &:hover{
    transition: all 0.2s ease-in-out;
    background:var(--White);
    color:var(--GreenStrong);
  }

  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
    padding:0px 0px; 
    height:80px;
  }
`

export const ButtonR = styled(LinkR)`
  background: ${({primary}) => (primary ? 'var(--PinkStrong)' : 'var(--PinkStrong)')};
  white-space:nowrap;
  padding:${({big}) => (big ? '14px 38px' : '12px 30px')};
  color:${({dark}) => (dark ? 'var(--White)' : 'var(--White)')};
  font-size:${({fontBig}) => (fontBig ? '20px' : '16px')};
  outline:none;
  border:2px solid var(--White); 
  cursor:pointer;
  text-decoration:none;
  display:flex;
  width:250px;
  justify-content:center;
  align-items:center;
  transition: all 0.2s ease-in-out;

  &:hover{
    transition: all 0.2s ease-in-out;
    background: ${({primary}) => (primary ? 'var(--White)' : 'var(--White)')};
    color:${({dark}) => (dark ? 'var(--PinkStrong)' : 'var(--PinkStrong)')};
  }
  @media screen and (max-width: 723px){
    transition: all 0.2s ease-in-out;
    width:180px;
  }
  @media screen and (max-width: 480px){
    transition: all 0.2s ease-in-out;
    width:200px;
  }

`;
