import React, {useState, useEffect} from 'react';
import {FaStar} from 'react-icons/fa'
import {PromotionContainer, PromotionWrapper, PromotionsCard, ImgPromotion, PromotionContent, PromotionHeader, PromotionDescription, PromotionCardHeader, PromotionCardDescription, PromotionCardTag, PromotionCardTitle, PromotionCardColumn, PromotionDiscount, ButtonPromotions} from './PromotionsElements';
import LogPopUpComponent from '../LogPopUp/index';
import NotificationBandSection from '../NotificationBand';

const PromotionsSection = ({PromotionsData, user, cart, setPropCart}) => {

  const [popState, setPopState] = useState(false);
  const [popMessage, setPopMessage] = useState(null);
  const [popHeader, setPopHeader] = useState(null);
  const [selectedPromotion, setSelectedPromotion] = useState(0);
  const [triggerSelection, setTriggerSelection] = useState(false);

  useEffect(() => {
    setSelectedPromotion(0);
    setPropCart("idPromotion", 0);
  }, [])

  const triggerReservePromotion = (event) => {
    event.preventDefault();
    var id = event.target.id
    if (user == null) {
      setPopState(true);
      setPopHeader("No ha Iniciado Sesión")
      setPopMessage("Tiene que iniciar Sesión para poder acceder a esta promoción.")
    } else {
      setPopState(false);
      if (selectedPromotion == Number(id)) {
        setSelectedPromotion(0);
        setPropCart("idPromotion", 0);
      } else {
        setSelectedPromotion(0);
        setPropCart("idPromotion", 0);
        setSelectedPromotion(Number(id));
        setPropCart("idPromotion", Number(id));
        setTriggerSelection(true);
      }
    }
  }

  /*******************************NOTIFCATION VARIABLES************************************************/
  const [typeNotification, setTypeNotification] = useState("Success");
  const [MessageNotification, setMessageNotification] = useState("No Message");
  const [TriggerNotification, setTriggerNotification] = useState(false);

  const AppearNotification = async () => {
    await new Promise((r) => setTimeout(r, 4000));
    await setTriggerNotification(false);
  };

  useEffect(() => {
    if (triggerSelection === true) {
      setTypeNotification("Success");
      setMessageNotification("Sube y selecciona las fechas para ver la disponibilidad de las tiendas en la promoción seleccionada");
      setTriggerNotification(true);
      AppearNotification();
      setTriggerSelection(false);
    }
  }, [triggerSelection])

  return (
    <PromotionContainer id="promotions">
      <LogPopUpComponent header={popHeader} message={popMessage} Open={popState} setOpen={setPopState} />
      <NotificationBandSection type={typeNotification} message={MessageNotification} trigger={TriggerNotification} />
      <PromotionHeader>Disfruta de Nuestras Promociones</PromotionHeader>
      <PromotionDescription>Aprovecha esta oportunidad</PromotionDescription>
      <PromotionWrapper>
        {PromotionsData.length !== 0 && (
          PromotionsData.map((promo, index) => {
            return (
              <PromotionsCard id={promo.idpromotion} key={index}>
                <ImgPromotion imgsrc={promo.imgroute}>
                </ImgPromotion>
                <PromotionContent>
                  <PromotionCardHeader>Bambucamp Glamping</PromotionCardHeader>
                  <PromotionCardTag>
                    <FaStar /><FaStar /><FaStar /><FaStar /><FaStar />
                  </PromotionCardTag>
                  <PromotionCardColumn>
                    <PromotionCardTitle>{promo.description}</PromotionCardTitle>
                  </PromotionCardColumn>
                  <PromotionCardDescription>Descuento de hasta</PromotionCardDescription>
                  <PromotionDiscount>{promo.discount}%</PromotionDiscount>
                  {selectedPromotion == promo.idpromotion ?
                    <ButtonPromotions id={promo.idpromotion} onClick={(event) => triggerReservePromotion(event)} className="Quitar">Quitar</ButtonPromotions> :
                    <ButtonPromotions id={promo.idpromotion} onClick={(event) => triggerReservePromotion(event)}>Agregar</ButtonPromotions>
                  }
                </PromotionContent>
              </PromotionsCard>
            )
          }))}
      </PromotionWrapper>
    </PromotionContainer>
  );
};
export default PromotionsSection;
