import React, {useState, useEffect} from 'react';
import {useLocation} from 'react-router-dom';
import LoadingPage from '../components/LoadingPage';
import NavCart from '../components/CartSection/NavCart';
import HeroSection from '../components/HeroSection';
import Footer from '../components/Footer';
import {CartFirstSection} from '../components/CartSection/CartFirstSection';
import axios from 'axios';
import {PATH_PREFIX_BACKEND} from "../Data/EnvironmentVariables"

const TentSection = ({cart, setPropCart, AvailableTents, setAvailableTents}) => {

  let location = useLocation();
  const [loading, setLoading] = useState(false);
  const [accessoAPI, setAccesoAPI] = useState({tipo: 'GET', url: PATH_PREFIX_BACKEND + '/api/tents'});
  const [datos, setDatos] = useState([])
  const [errorAPI, setErrorAPI] = useState(null);
  const [respuestaAPI, setRespuestaAPI] = useState([]);

  useEffect(() => {
    const consultaAPI = async () => {
      setErrorAPI(null);
      try {
        const consulta = await axios({method: accessoAPI.tipo, url: accessoAPI.url, data: datos})
        consulta.data['data'].forEach(item => {
          var arrStrStr = item.price_from_date.split('-')
          var arrStrEnd = item.price_to_date.split('-')
          item.price_from_date = new Date(Number(arrStrStr[0]), Number(arrStrStr[1]) == 1 ? 0 : (Number(arrStrStr[1]) - 1), Number(arrStrStr[2]))
          item.price_to_date = new Date(Number(arrStrEnd[0]), Number(arrStrEnd[1]) == 1 ? 0 : (Number(arrStrEnd[1]) - 1), Number(arrStrEnd[2]))
        })
        setRespuestaAPI(consulta.data['data']);
      } catch (error) {
        setErrorAPI(error.response)
      }
      setLoading(true);
    };
    consultaAPI();
  }, [accessoAPI.tipo, accessoAPI.url, datos])


  if (loading === false) {
    return <LoadingPage />
  } else {
    return (
      <>
        <NavCart section={true} step={1} />
        <HeroSection section={'cart-1'} cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} />
        <CartFirstSection cart={cart} setPropCart={setPropCart} tentData={respuestaAPI} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} />
        <Footer />
      </>
    )

  }
}

export default TentSection;
