import React, {useState, useEffect} from 'react'
import {useNavigate} from "react-router-dom";
import {Calendar} from './Calendar';
import {GuestBar} from './GuestElements';
import {BookingbarContainer, BookingbarWrapper, Column1, Column2, Column3, Column4, Column5, InputCheckInDate, InputCheckOutDate, Guests, Destination, Booking, IconMap, IconUser, IconDate, TopLineSelection} from './BookingbarElements';
import Moment from 'moment';
import axios from 'axios';
import {PATH_PREFIX_BACKEND} from "../../Data/EnvironmentVariables"
import NotificationBandSection from '../NotificationBand';
import LoadingAsync from '../LoadingAsync';



const Bookingbar = ({activateBookingBar, cart, setPropCart, AvailableTents, setAvailableTents}) => {
  let navigate = useNavigate();

  const [popState, setPopState] = useState(false);
  const [CheckInDate, setCheckInDate] = useState(cart.checkInDate);
  const [CheckOutDate, setCheckOutDate] = useState(cart.checkOutDate);
  const [OpenCalendarCheckIn, setOpenCalendarCheckIn] = useState(false);
  const [OpenCalendarCheckOut, setOpenCalendarCheckOut] = useState(false);

  const [OpenGuestBar, setOpenGuestBar] = useState(false);

  const OpenCalendarFunction = (calendar) => {
    if (calendar === "1") {
      setOpenCalendarCheckIn(!OpenCalendarCheckIn);
      if (OpenCalendarCheckOut === true) {
        setOpenCalendarCheckOut(!OpenCalendarCheckOut);
      }
      if (OpenGuestBar === true) {
        setOpenGuestBar(!OpenGuestBar);
      }
    } else {
      setOpenCalendarCheckOut(!OpenCalendarCheckOut);
      if (OpenCalendarCheckIn === true) {
        setOpenCalendarCheckIn(!OpenCalendarCheckIn);
      }
      if (OpenGuestBar === true) {
        setOpenGuestBar(!OpenGuestBar);
      }
    }
  }


  const [Adults, setAdults] = useState(cart.qtyAdults);
  const [Kids, setKids] = useState(cart.qtyKids);
  const [Babys, setBabys] = useState(0)
  const [Guest, setGuest] = useState(0);

  const OpenGuestBarFunction = () => {
    setOpenGuestBar(!OpenGuestBar)
    if (OpenCalendarCheckOut === true) {
      setOpenCalendarCheckOut(!OpenCalendarCheckOut);
    }
    if (OpenCalendarCheckIn === true) {
      setOpenCalendarCheckIn(!OpenCalendarCheckIn);
    }
  }

  const IncrementPeople = (variable, setfunction) => {
    if (variable < 6) {
      setfunction(variable + 1);
    }
  }

  const DecrementPeople = (variable, setfunction) => {
    if (variable > 0) {
      setfunction(variable - 1);
    }
  }

  useEffect(() => {
    if ((Adults + Kids) < 13) {
      setGuest(Adults + Kids);
    }
  }, [Adults, Kids])


  const [step, setStep] = useState('1');

  const changeStepBookingBar = (event) => {
    setStep(event.target.id);
  }
  useEffect(() => {
    const stringStep = '*[id^="BookingStep"]'
    const AllStepElements = document.querySelectorAll(stringStep);
    AllStepElements.forEach(function (element) {
      element.classList.remove('isActive')
    });
    const stepElement = document.getElementById(`BookingStep-${step}`);
    stepElement.classList.add('isActive');
  }, [step])

  const updateData = () => {
    setPropCart("checkInDate", CheckInDate);
    setPropCart("checkOutDate", CheckOutDate);
    setPropCart("qtyAdults", Adults);
    setPropCart("qtyKids", Kids);
    localStorage.setItem("qtyAdults", JSON.stringify(Adults));
    localStorage.setItem("qtyKids", JSON.stringify(Kids));
  }
  //Update Data

  const [accessoAPI, setAccesoAPI] = useState({tipo: 'POST', url: PATH_PREFIX_BACKEND + '/api/reserves/'});
  const [datos, setDatos] = useState({})
  const [errorAPI, setErrorAPI] = useState(null);
  const [respuestaAPI, setRespuestaAPI] = useState({respuesta: 'KO'});

  useEffect(() => {
    setDatos({
      CheckInDate: CheckInDate.toLocaleDateString("fr-CA"),
      CheckOutDate: CheckOutDate.toLocaleDateString("fr-CA"),
      idPromotion: cart.idPromotion
    })
    localStorage.setItem("checkInDate", JSON.stringify(CheckInDate));
    localStorage.setItem("checkOutDate", JSON.stringify(CheckOutDate));
  }, [CheckInDate, CheckOutDate, cart.idPromotion]);

  const sendData = async (e) => {
    e.preventDefault();
    updateData();
    const consultaAPI = async () => {
      setErrorAPI(null);
      try {
        const consulta = await axios({method: accessoAPI.tipo, url: accessoAPI.url, data: datos})
        await setPopState(false)
        if (consulta.data.class === "booking") {
          setAvailableTents(consulta.data.tents)
          await navigate('/cart-tents')
        } else if (consulta.data.class === "promotion") {
          await navigate('/cart-payment')
          localStorage.setItem("selectedPromotion", JSON.stringify(consulta.data.promotion[0]));
        }
      } catch (error) {
        setErrorAPI(error.response.data)
        await setPopState(false)
      }
    };
    setPopState(true)
    consultaAPI();

  }

  /*******************************NOTIFCATION VARIABLES************************************************/
  const [typeNotification, setTypeNotification] = useState("Success");
  const [MessageNotification, setMessageNotification] = useState("No Message");
  const [TriggerNotification, setTriggerNotification] = useState(false);

  const AppearNotification = async () => {
    await new Promise((r) => setTimeout(r, 4000));
    await setTriggerNotification(false);
  };

  useEffect(() => {
    if (errorAPI !== null) {
      setTypeNotification("Error");
      setMessageNotification(errorAPI.status);
      setTriggerNotification(true);
      AppearNotification();
      setErrorAPI(null);
    }
  }, [errorAPI])

  return (
    <>
      <LoadingAsync Open={popState} />
      <NotificationBandSection type={typeNotification} message={MessageNotification} trigger={TriggerNotification} />
      <BookingbarContainer activateBookingBar={activateBookingBar}>
        <GuestBar OpenGuestBar={OpenGuestBar} IncrementPeople={IncrementPeople} DecrementPeople={DecrementPeople} Adults={Adults} setAdults={setAdults} Kids={Kids} setKids={setKids} Babys={Babys} setBabys={setBabys} />
        <BookingbarWrapper onSubmit={(event) => {sendData(event)}}>
          <Column1>
            <TopLineSelection id="BookingStep-1" lenght={1} step={1} />
            <IconMap />
            <Destination id='1'>Bambucamp Glamping</Destination>
          </Column1>
          <Column2>
            <TopLineSelection id="BookingStep-2" lenght={1} step={2} />
            <IconDate />
            <InputCheckInDate id='2' name='CheckInDate' onClick={(event) => {event.stopPropagation(); changeStepBookingBar(event); OpenCalendarFunction("1")}} value={Moment(CheckInDate).format('DD-MM-YYYY')} readOnly />
            <Calendar OpenCalendar={OpenCalendarCheckIn} CalendarType='CheckIn' changeDate={setCheckInDate} referenceDate={CheckOutDate} />
          </Column2>
          <Column3>
            <TopLineSelection id="BookingStep-3" lenght={1} step={3} />
            <IconDate />
            <InputCheckOutDate id='3' name='CheckOutDate' onClick={(event) => {event.stopPropagation(); changeStepBookingBar(event); OpenCalendarFunction("2")}} value={Moment(CheckOutDate).format('DD-MM-YYYY')} readOnly />
            <Calendar OpenCalendar={OpenCalendarCheckOut} CalendarType='CheckOut' changeDate={setCheckOutDate} referenceDate={CheckInDate} />
          </Column3>
          <Column4>
            <TopLineSelection id="BookingStep-4" lenght={0.4} step={4} />
            <IconUser />
            <Guests id='4' name='Guest' onClick={(event) => {event.stopPropagation(); changeStepBookingBar(event); OpenGuestBarFunction()}} value={Guest} readOnly />
          </Column4>
          <Column5>
            <Booking />
          </Column5>
        </BookingbarWrapper>
      </BookingbarContainer>
    </>
  );
};

export default Bookingbar;
