import React, {useState, useEffect} from 'react';
import './App.css';
import {BrowserRouter as Router, Routes, Route} from 'react-router-dom'
import Home from './pages/index';
import InfoPage from './pages/section';
import ExperiencePage from './pages/experiencesection';
import TentSection from './pages/cartFirstSection';
import ExtraSection from './pages/cartSecondSection';
import AddressSection from './pages/cartThirdSection';
import ConfirmationSection from './pages/cartFourSection';
import SigninPage from '../src/pages/signin';
import UserPanelPage from './pages/userPanel';
import UserProfilePage from './pages/userProfile'
import RegisterPage from './pages/register';
import RecoverPasswordPage from './pages/recoverpassword'



function App() {
  const [cart, setCart] = useState({});
  const [AvailableTents, setAvailableTents] = useState([]);
  const [token, setToken] = useState(null);
  const [user, setUser] = useState(null);

  const CloseSession = () => {
    setUser(null);
    setToken(null);
    localStorage.removeItem("user")
    localStorage.removeItem("token")
  }

  useEffect(() => {
    var userVar = localStorage.getItem("user") !== null ? JSON.parse(localStorage.getItem("user")) : null;
    var tokenVar = localStorage.getItem("token") !== null ? JSON.parse(localStorage.getItem("token")) : null;
    setUser(userVar);
    setToken(tokenVar)
  }, [])

  useEffect(() => {
    var localCheckInDate = localStorage.getItem("checkInDate") !== null ? new Date(localStorage.getItem("checkInDate").substring(1, 11) + " " + localStorage.getItem("checkInDate").substring(12, 20)) : new Date();
    var localCheckOutDate = localStorage.getItem("checkOutDate") !== null ? new Date(localStorage.getItem("checkOutDate").substring(1, 11) + " " + localStorage.getItem("checkOutDate").substring(12, 20)) : new Date();
    var localqtyAdults = localStorage.getItem("qtyAdults") !== null ? JSON.parse(localStorage.getItem("qtyAdults")) : 2;
    var localqtyKids = localStorage.getItem("qtyKids") !== null ? JSON.parse(localStorage.getItem("qtyKids")) : 0;
    var localqtyAdultsAd = localStorage.getItem("AditionalPeople") !== null ? JSON.parse(localStorage.getItem("AditionalPeople")) : 0;
    var localidtents = localStorage.getItem("idtents") !== null ? JSON.parse(localStorage.getItem("idtents")) : [];
    var localproducts = localStorage.getItem("products") !== null ? JSON.parse(localStorage.getItem("products")) : {};
    var localexperiences = localStorage.getItem("experiences") !== null ? JSON.parse(localStorage.getItem("experiences")) : {};
    var localAmountTotal = localStorage.getItem("AmountTotal") !== null ? JSON.parse(localStorage.getItem("AmountTotal")) : 0;
    var localSaleDate = localStorage.getItem("SaleDate") !== null ? JSON.parse(localStorage.getItem("SalesDate")) : new Date();
    var localidPromotion = localStorage.getItem("idPromotion") !== null ? JSON.parse(localStorage.getItem("idPromotion")) : 0;
    var localidguest = localStorage.getItem("idguest") !== null ? JSON.parse(localStorage.getItem("idguest")) : null;
    var localtotalImportTents = localStorage.getItem("totalImportTents") !== null ? JSON.parse(localStorage.getItem("totalImportTents")) : 0;
    setCart(({
      'checkInDate': localCheckInDate,
      'checkOutDate': localCheckOutDate,
      'qtyAdults': localqtyAdults,
      'qtyKids': localqtyKids,
      'AditionalPeople': localqtyAdultsAd,
      'idtents': localidtents,
      'products': localproducts,
      'experiences': localexperiences,
      'AmountTotal': localAmountTotal,
      'SaleDate': localSaleDate,
      'idPromotion': localidPromotion,
      'idguest': localidguest,
      'totalImportTents': localtotalImportTents
    }));
  }, [])

  const setPropCart = (name, var_property) => {
    setCart(prevCart => ({
      ...prevCart,
      [name]: var_property,
    }));
  }
  return (
    <Router>
      <Routes>
        <Route path='/' element={<Home cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} user={user} token={token} CloseSession={CloseSession} />} />
        <Route path='section/experience/family' element={<ExperiencePage cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} keyword="family" user={user} CloseSession={CloseSession} />} />
        <Route path='section/experience/adults' element={<ExperiencePage cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} keyword="adults" user={user} CloseSession={CloseSession} />} />
        <Route path='section/experience/bambucamp' element={<ExperiencePage cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} keyword="bambucamp" user={user} CloseSession={CloseSession} />} />
        <Route path='section/experience/aventuras' element={<ExperiencePage cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} keyword="aventuras" user={user} CloseSession={CloseSession} />} />
        <Route path='section/experience/relaxing' element={<ExperiencePage cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} keyword="relaxing" user={user} CloseSession={CloseSession} />} />
        <Route path='section/experience/bodas' element={<ExperiencePage cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} keyword="bodas" user={user} CloseSession={CloseSession} />} />
        <Route path='section/generalconditions' element={<InfoPage keyword="general" cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} user={user} CloseSession={CloseSession} />} />
        <Route path='section/cookiespolicy' element={<InfoPage keyword="cookies" cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} user={user} CloseSession={CloseSession} />} />
        <Route path='section/privacypolicity' element={<InfoPage keyword="privacy" cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} user={user} CloseSession={CloseSession} />} />
        <Route path='/cart-tents' element={<TentSection cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} />} />
        <Route path='/cart-extras' element={<ExtraSection cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} />} />
        <Route path='/cart-payment' element={<AddressSection token={token} user={user} cart={cart} setPropCart={setPropCart} AvailableTents={AvailableTents} setAvailableTents={setAvailableTents} />} />
        <Route path='/cart-confirmation' element={<ConfirmationSection token={token} user={user} cart={cart} setPropCart={setPropCart} />} />
        <Route path="/signin" element={<SigninPage token={token} setToken={setToken} setUser={setUser} user={user} />} />
        <Route path="/userpanel" element={<UserPanelPage token={token} user={user} CloseSession={CloseSession} />} />
        <Route path="/userprofile" element={<UserProfilePage token={token} user={user} setUser={setUser} CloseSession={CloseSession} />} />
        <Route path="/register" element={<RegisterPage />} />
        <Route path="/recoverpassword" element={<RecoverPasswordPage />} />
      </Routes>
    </Router>
  );
}

export default App;
