import React, {useState} from 'react';
import {FloatingButtonWhats, FaWhatsIcon, Label} from './FloatingButtonElements';

const FloatingButton = () => {
  const [isOpen, setIsOpen] = useState(false)

  const toogle = () => {
    setIsOpen(true)
  }
  const toogleFalse = () => {
    setIsOpen(false)
  }
  return (
    <>
      <FloatingButtonWhats onMouseOver={toogle} onMouseLeave={toogleFalse} href="https://wa.link/ajh6ae" target="_blank"><FaWhatsIcon /></FloatingButtonWhats>
      <Label isOpen={isOpen}>Consulta ahora</Label>
    </>
  )
}

export default FloatingButton;
