export const serializeDates = (data) => {
  for (let item of data) {
    var localdateFrom = formatDataDate(item.dateFrom);
    item.dateFrom = localdateFrom;
    var localdateTo = formatDataDate(item.dateTo);
    item.dateTo = localdateTo;
    var locadateSale = formatDataDate(item.dateSale);
    item.dateSale = locadateSale;

  }
  return data;
}

export const formatDataDate = (date) => {
  var full_date = date.split("T");
  var data_date = full_date[0].split("-");
  var formatted_date = new Date(data_date[0], data_date[1] - 1, data_date[2]);
  return formatted_date
}
