import styled from 'styled-components';

export const WarningContainer = styled.div`
  position: absolute;
  top:0;
  height: 100vh;
  width: 100vw;
  background:linear-gradient(rgba(0, 0, 0, 0.4),rgba(0, 0, 0, 0.4),rgba(0, 0, 0, 0.4));
  display:${({activeSection}) => (activeSection ? 'flex' : 'none')};
  flex-direction:column;
  align-items:center;
  justify-content:flex-start;
  z-index:10000;
`;

export const WarningWrapper = styled.div`
  position:relative;
  height:40vh;
  width:40vw;
  margin-Top:5vh;
  border-radius:5vh;
  background:var(--White);
  border:1px solid var(--GreenStrong);
  overflow:hidden;

  @media screen and (max-width:480px){
    width:90vw;
    height:70vh;
  }
`;

export const ContentWrapper = styled.div`
  height:100%;
  width:100%;
  border-radius:5vh 5vh 5vh 5vh;
  padding:5vh;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
`
export const Title = styled.h2`
  color:var(--GreenLight); 
`;
export const Message = styled.p`
  margin-top:1vh;
  color:var(--GreenLight); 
`
export const WrapperButtons = styled.div`
  width:100%;
  height:auto;
  display:flex;
  flex-direction:row;
  align-items:center;
  justify-content:space-around;
  margin:1vh 0;
  padding:1vh;
  @media screen and (max-width:480px){
    flex-direction:column;
    justify-content:center;
  }
`;

export const ButtonItem = styled.div`
  @media screen and (max-width:480px){
    margin:1vh 0;
  }
`

export const InputReasons = styled.textarea`
  width:100%;
  min-height:8vh;
  padding:2.5vh;
  border-radius:2.5vh;
  outline:none;
`;
export const LabelRed = styled.label`
  width:100%;
  height:auto;
  text-align:right;
  color:red;
  margin:0vh 0 2vh 0;
  opacity:0;

  @media screen and (max-width:480px){
    font-size:1.5vh;
  }

  &.isActive{
    opacity:1;
  }
`


