import styled from 'styled-components';
import {Link as LinkR} from 'react-router-dom'

export const PromotionContainer = styled.div`
  height:auto;
  width:100%;
  background:var(--GreenLight);
  display:flex;
  padding: 120px 80px 0px 80px;
  flex-direction:column;
  justy-content:center;
  align-items:center;

  @media screen and (max-width: 1200px){
    transition: all 0.2s ease-in-out;
    height:auto;
  }
`;
export const PromotionWrapper = styled.div` 

  display: grid;
  align-items: center;
  justify-content:center;
  margin:40px;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows:auto;
  grid-gap:60px;
  height: 100%;
  width: 100%;
  transition: all 0.2s ease-in-out;

  @media screen and (max-width: 1200px){
    transition: all 0.2s ease-in-out;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows:auto auto;
  }
  @media screen and (max-width: 1000px){
    transition: all 0.2s ease-in-out;
    grid-template-columns: 1fr 1fr;
    grid-template-rows:auto auto;
  }
  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
    grid-template-columns: 1fr;
    grid-template-rows:auto auto auto auto;
    grid-gap:60px;
  }
`;
export const PromotionsCard = styled.div`
  display:flex;
  flex-direction:column;
  justify-content:flex-start;
  align-items:center;
  width:100%;
  min-height:400px;
  
  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
    min-height:200px;
  }
`;
export const ImgPromotion = styled.div`
  height:250px;
  width:100%;
  background-image:url(${props => props.imgsrc});
  background-size:cover;
  border-radius: 5% 5% 0 0;

  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
    display:none;
  }
`;
export const PromotionContent = styled.div` 
  display:flex;
  flex-direction:column;
  justify-content:flex-start;
  align-items:flex-start;
  height:50%;
  width:100%;
  padding:10px;
  background:var(--White);
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  border-radius: 0 0 5% 5%;


  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
    height:100%;
    border-radius: 5% 5% 5% 5%;
  }
`;
export const PromotionHeader = styled.h1`
  text-align:center;
  color:var(--White);
  font-family:var(--Header-Family);
  font-size: 40px;
`;
export const PromotionDescription = styled.p`
  text-align:center;
  color:var(--PinkLight);
  font-family:var(--Text-Family-Secondary)
  font-size: 18px;
`;
export const PromotionCardHeader = styled.h2`
  text-align:left;
  color:var(--GreenStrong);
  font-size: 14px;
`;


export const PromotionCardTag = styled.p`
  text-align:left;
  color:var(--GreenLight);
  font-size: 20px;
`;

export const PromotionCardTitle = styled.h2`
  text-align:left;
  color:var(--GreenLight);
  font-size: 14px;
`;

export const PromotionCardColumn = styled.div`
  display:flex;
  justify-content:flex-start;
  flex-wrap:nowrap;
  flex-direction:column;
  width:100%;
  height:80px;
`;

export const PromotionCardDescription = styled.p`
  text-align:left;
  color:var(--GreenLight);
  font-family:var(--Text-Family-Secondary)
  font-size: 18px;
`;
export const PromotionDiscount = styled.p`
  text-align:left;
  color:var(--GreenLight);
  font-family:var(--Text-Family-Secondary)
  font-weight:bold;
  font-size: 35px;
`;

export const ButtonPromotions = styled.button` 
  background:var(--White);
  white-space:nowrap;
  padding:20px;
  color:var(--GreenLight);
  font-size:20px;
  outline:none;
  border:2px solid var(--GreenStrong); 
  cursor:pointer;
  width:100%;
  height:40px;

  display:flex;
  justify-content:center;
  align-items:center;
  text-decoration:none;
  transition: all 0.2s ease-in-out;

  &:hover{
    transition: all 0.2s ease-in-out;
    background:var(--GreenStrong);
    color:var(--White);
  }

  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
  }
  &.Quitar{
    border:2px solid #00AAA9; 
    background:var(--GreenStrong);
    color:var(--White);
  }
`
export const PromotionPrice = styled.p`
  text-align:right;
  color:var(--GreenLight);
  font-family:var(--Text-Family-Secondary)
  font-style:italic;
  font-size: 18px;
`;
