import React from 'react'
import {BandContainer, BandWrapper, BandIcon, BandContent, IconSuccess, IconWarning, IconError} from './NotificationElements';

const NotificationBandSection = ({type, message, trigger}) => {

  if (type === "Success") {
    return (
      <BandContainer activeNotification={trigger}>
        <BandWrapper>
          <BandIcon>
            <IconSuccess />
          </BandIcon>
          <BandContent>
            {message}
          </BandContent>
        </BandWrapper>
      </BandContainer>
    )
  } else if (type === "Warning") {
    return (
      <BandContainer activeNotification={trigger}>
        <BandWrapper>
          <BandIcon>
            <IconWarning />
          </BandIcon>
          <BandContent>
            {message}
          </BandContent>
        </BandWrapper>
      </BandContainer>
    )
  } else if (type === "Error") {
    return (
      <BandContainer activeNotification={trigger}>
        <BandWrapper>
          <BandIcon>
            <IconError />
          </BandIcon>
          <BandContent>
            {message}
          </BandContent>
        </BandWrapper>
      </BandContainer>
    )
  }
}

export default NotificationBandSection;
