import React, {useState} from 'react';
import ImgExperienceF from '../../images/Experiences/Grid1.jpg'
import ImgExperienceA from '../../images/Experiences/Grid2.jpg'
import ImgExperienceD from '../../images/Experiences/Grid3.jfif'
import ImgExperienceB from '../../images/Experiences/Grid4.jpg'
import ImgExperienceEN from '../../images/Experiences/Grid5.jpg'
import ImgExperienceBO from '../../images/Experiences/Grid6.webp'
import {ExperienceContainer, ExperienceSubtitle, ExperienceWrapper, GridElement, ExperienceLink, Title, Header, Subtitle, ExperienceHeader, ExperienceWrapperMobileView, ExperienceElement} from './ExperiencesElements';
import {ButtonExperience} from '../ButtonElements';
import {ArrowForward, ArrowRight} from '../HeroSection/HeroElements';
import {animateScroll as scroll} from 'react-scroll';

const Experiences = () => {

  const [hover, setHover] = useState(false)
  const onHover = () => {
    setHover(!hover)
  }
  const toggleTop = () => {
    scroll.scrollToTop();
  };


  return (
    <ExperienceContainer id="experiences">
      <ExperienceHeader>Disfruta tu estancia</ExperienceHeader>
      <ExperienceSubtitle>Encuentra el tipo de viaje que más se adapte a ti</ExperienceSubtitle>
      <ExperienceWrapper>
        <GridElement gridarea='a' imgsrc={ImgExperienceA}>
          <ExperienceLink to="/section/experience/family" onClick={toggleTop}>
            <Title>Bambucamp</Title>
            <Header>Familia y Amigos</Header>
            <Subtitle>Paquetes Especiales</Subtitle>
          </ExperienceLink>
        </GridElement>
        <GridElement gridarea='f' imgsrc={ImgExperienceF}>
          <ExperienceLink to="/section/experience/adults" onClick={toggleTop}>
            <Title>Bambucamp</Title>
            <Header>Solo para parejas</Header>
            <Subtitle>Ver Más</Subtitle>
          </ExperienceLink>
        </GridElement>
        <GridElement gridarea='d' imgsrc={ImgExperienceD}>
          <ExperienceLink to="/section/experience/aventuras" onClick={toggleTop}>
            <Title>Bambucamp</Title>
            <Header>Aventuras</Header>
            <Subtitle>Lugares de Aventuras</Subtitle>
          </ExperienceLink>
        </GridElement>
        <GridElement gridarea='b' imgsrc={ImgExperienceB}>
          <ExperienceLink to="/section/experience/bambucamp" onClick={toggleTop}>
            <Title>Bambucamp</Title>
            <Header>Gastronomía y coctéles</Header>
            <Subtitle>Antes y Durante tu estacia</Subtitle>
          </ExperienceLink>
        </GridElement>
        <GridElement gridarea='en' imgsrc={ImgExperienceEN}>
          <ExperienceLink to="/section/experience/relaxing" onClick={toggleTop}>
            <Title>Bambucamp</Title>
            <Header>Regálate una experiencia</Header>
            <Subtitle>Disfrútalo</Subtitle>
          </ExperienceLink>
        </GridElement>
        <GridElement gridarea='bo' imgsrc={ImgExperienceBO}>
          <ExperienceLink to="/section/experience/bodas" onClick={toggleTop} >
            <Title>Bambucamp</Title>
            <Header>Tus Glampings</Header>
            <Subtitle>Vive Momentos Inolvidables</Subtitle>
          </ExperienceLink>
        </GridElement>
      </ExperienceWrapper>
      <ExperienceWrapperMobileView>
        <ExperienceElement>
          <ButtonExperience to='/section/experience/adults' onClick={toggleTop} onMouseEnter={onHover} onMouseLeave={onHover}>Solo para parejas{hover ? <ArrowForward /> : <ArrowRight />}</ButtonExperience>
        </ExperienceElement>
        <ExperienceElement>
          <ButtonExperience to='/section/experience/family' onClick={toggleTop} onMouseEnter={onHover} onMouseLeave={onHover}>Familia y Amigos{hover ? <ArrowForward /> : <ArrowRight />}</ButtonExperience>
        </ExperienceElement>
        <ExperienceElement>
          <ButtonExperience to='/section/experience/aventuras' onClick={toggleTop} onMouseEnter={onHover} onMouseLeave={onHover}>Aventuras{hover ? <ArrowForward /> : <ArrowRight />}</ButtonExperience>
        </ExperienceElement>
        <ExperienceElement>
          <ButtonExperience to='/section/experience/bambucamp' onClick={toggleTop} onMouseEnter={onHover} onMouseLeave={onHover}>Gastronomía y coctéles{hover ? <ArrowForward /> : <ArrowRight />}</ButtonExperience>
        </ExperienceElement>
        <ExperienceElement>
          <ButtonExperience to='/section/experience/relaxing' onClick={toggleTop} onMouseEnter={onHover} onMouseLeave={onHover}>Regálate una experiencia{hover ? <ArrowForward /> : <ArrowRight />}</ButtonExperience>
        </ExperienceElement>
        <ExperienceElement>
          <ButtonExperience to='/section/experience/bodas' onMouseEnter={onHover} onMouseLeave={onHover}>Tus Glampings{hover ? <ArrowForward /> : <ArrowRight />}</ButtonExperience>
        </ExperienceElement>
      </ExperienceWrapperMobileView>
    </ExperienceContainer>
  );
};


export default Experiences;
