import React, {useState, useEffect} from 'react';
import {SectionInfoContainer, SectionWrapper, SectionTitle, SectionP, SectionSubtitle, ContentWrapper} from './SectionInfoElements';
import {GeneralConditionsObject} from '../../Data/GeneralConditions';
import {CookiesObject} from '../../Data/Cookies';
import {PaymentAndSecurityObject} from '../../Data/PaymentAndSecurity';
import {PrivacyPolicityObject} from '../../Data/PrivacyPolicity';

const InfoSectionComponent = ({keyword}) => {
  const [SectionObject, setSectionObject] = useState(
    {
      'title': null,
      'description': null,
      'content': [
        {
          'title': null,
          'description': null,
        }]
    }
  );
  useEffect(() => {
    if (keyword === "general") {
      setSectionObject(GeneralConditionsObject)
    } else if (keyword === "privacy") {
      setSectionObject(PrivacyPolicityObject)
    } else if (keyword === "cookies") {
      setSectionObject(CookiesObject)
    } else if (keyword === "payment") {
      setSectionObject(PaymentAndSecurityObject)
    }
  }, [keyword, GeneralConditionsObject, PrivacyPolicityObject, CookiesObject, PaymentAndSecurityObject])
  return (
    <SectionInfoContainer>
      <SectionWrapper>
        <SectionTitle>{SectionObject.title}</SectionTitle>
        <SectionP>{SectionObject.description}</SectionP>
        {SectionObject.content.map((item, index) => (
          <ContentWrapper key={index}>
            <SectionSubtitle key={item.index}>{item.title}</SectionSubtitle>
            <SectionP key={item.index}>{item.description}</SectionP>
          </ContentWrapper>
        ))}
      </SectionWrapper>
    </SectionInfoContainer>
  );
};


export default InfoSectionComponent;
