export const ButtonExtraProduct = styled.input.attrs({type: 'checkbox'})`
    position: relative;
	  cursor: pointer;
    transition: all 0.2s ease-in-out;

    &:before{
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color:#e9e9e9;
    transition: all 0.2s ease-in-out;
    }
    &:checked:before{
     content: "";
     display: block;
     position: absolute;
     width: 100%;
     height: 100%;
     top: 0;
     left: 0;
     background-color:var(--GreenStrong);
    transition: all 0.2s ease-in-out;
    }
    &:checked:after{
      content: "";
      display: block;
      width: 25%;
      height: 50%;
      border: solid white;
      border-width: 0 2px 2px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
      position: absolute;
      top: 10%;
      left: 18%;
      transition: all 0.2s ease-in-out;
    }

`
