import React, {useState, useEffect} from 'react';
import {FaBars} from 'react-icons/fa'
import {useNavigate} from "react-router-dom";
import ImageLogo from '../../images/Banner/Logo1.png'
import {Nav, NavbarContainer, NavLogo, MobileIcon, NavMenu, NavItem, NavLinks, Logo, BottomLine, NavSection, NavMenuSection, NavButton, NavLogoSection, NavBtn, DropDown, NavItemDropDownList, DropItem, UserIcon, NameIcon, NavLinksDropDownList, NavLinksDropDownListDiv, NavBtnLink, BackgroundLogo} from './NavbarElements';
import {animateScroll as scroll} from 'react-scroll';

const Navbar = ({toggle, section, user, CloseSession}) => {
  const [scrollNav, setScrollNav] = useState(true)
  let navigate = useNavigate();

  const changeNav = () => {
    if (window.scrollY >= 80) {
      setScrollNav(false)
    } else {
      setScrollNav(true)
    }
  }

  const toggleHome = () => {
    scroll.scrollToTop();
  };

  useEffect(() => {
    window.addEventListener('scroll', changeNav);
  }, [])

  const RouteEscape = () => {
    CloseSession();
    navigate('/');
  }
  return (
    <>
      <Nav scrollnav={scrollNav} section={section}>
        <NavbarContainer>
          <NavLogo to="/" onClick={toggleHome} scrollnav={scrollNav} >
            <BackgroundLogo>
              <Logo src={ImageLogo} alt="Logo" />
            </BackgroundLogo>
          </NavLogo>
          <MobileIcon onClick={toggle}>
            <FaBars />
          </MobileIcon>
          <NavMenu>
            <NavItem>
              <NavLinks to="us" scrollnav={scrollNav} smooth={true} duration={500} spy={true} exact='true' offset={-140} activeClass="active">NOSOTROS</NavLinks>
            </NavItem>
            <NavItem>
              <NavLinks to="promotions" scrollnav={scrollNav} smooth={true} duration={500} spy={true} exact='true' offset={0} activeClass="active">OFERTAS</NavLinks>
            </NavItem>
            <NavItem>
              <NavLinks to="experiences" scrollnav={scrollNav} smooth={true} duration={500} spy={true} exact='true' offset={20} activeClass="active">EXPERIENCIAS</NavLinks>
            </NavItem>
            <NavItem>
              <NavLinks to="contactus" scrollnav={scrollNav} smooth={true} duration={500} spy={true} exact='true' offset={20} activeClass="active">CONTACTANOS</NavLinks>
            </NavItem>
            <NavBtn>
              {user !== null && (
                <DropDown>
                  <NavItemDropDownList><UserIcon /><NameIcon>{user.name}</NameIcon></NavItemDropDownList>
                  <DropItem>
                    <NavLinksDropDownList to="/">Inicio</NavLinksDropDownList>
                    <NavLinksDropDownList to="/userprofile">Ver Perfil</NavLinksDropDownList>
                    <NavLinksDropDownList to="/userpanel">Panel de Usuario</NavLinksDropDownList>
                    <NavLinksDropDownListDiv onClick={() => {RouteEscape()}}>Cerrar Sesión</NavLinksDropDownListDiv>
                  </DropItem>
                </DropDown>
              )}
              {user === null && (
                <NavBtnLink to="/signin">Iniciar Sesión</NavBtnLink>
              )}
            </NavBtn>
          </NavMenu>
        </NavbarContainer>
        <BottomLine scrollnav={scrollNav} />
      </Nav>
      <NavSection scrollnav={scrollNav} section={section}>
        <NavbarContainer>
          <NavLogoSection to="/" onClick={toggleHome} scrollnav={scrollNav} >
            <BackgroundLogo>
              <Logo src={ImageLogo} alt="Logo" />
            </BackgroundLogo>
          </NavLogoSection>
          <MobileIcon onClick={toggle}>
            <FaBars />
          </MobileIcon>
          <NavMenuSection>
            <NavBtn>
              {user !== null && (
                <DropDown>
                  <NavItemDropDownList><UserIcon /><NameIcon>{user.name}</NameIcon> <FaBars style={{color: '#E7FEFD'}} /></NavItemDropDownList>
                  <DropItem>
                    <NavLinksDropDownList to="/">Inicio</NavLinksDropDownList>
                    <NavLinksDropDownList to="/userprofile">Ver Perfil</NavLinksDropDownList>
                    <NavLinksDropDownList to="/userpanel">Panel de Usuario</NavLinksDropDownList>
                    <NavLinksDropDownListDiv onClick={() => {RouteEscape()}}>Cerrar Sesión</NavLinksDropDownListDiv>
                  </DropItem>
                </DropDown>
              )}
              {user === null && (
                <NavBtnLink to="/signin">Iniciar Sesión</NavBtnLink>
              )}
            </NavBtn>
          </NavMenuSection>
        </NavbarContainer>
      </NavSection>
    </>
  );
};

export default Navbar;
