import React, {useState, useEffect} from 'react';
import Navbar from '../components/Navbar';
import Band from '../components/Band';
import UserProfileSection from '../components/userProfile';
import LoadingPage from '../components/LoadingPage';
import SidebarSection from '../components/SideBar';


const UserProfilePage = ({token, user, setUser, CloseSession}) => {

  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const loadData = async () => {
      await new Promise((r) => setTimeout(r, 1200));
      setLoading((loading) => !loading);
    };
    loadData();
  }, []);

  const [isOpen, setIsOpen] = useState(false)

  const toggle = () => {
    setIsOpen(!isOpen)
  }

  if (loading === false) {
    return <LoadingPage />
  } else {
    return (
      <>
        <Band />
        <Navbar toggle={toggle} section={true} user={user} CloseSession={CloseSession} />
        <SidebarSection isOpen={isOpen} toggle={toggle} user={user} CloseSession={CloseSession} />
        <UserProfileSection user={user} token={token} setUser={setUser} />
      </>
    )

  }
}

export default UserProfilePage
