export const ButtonAdd = styled.input.attrs({type: 'checkbox'})`
    position: relative;
	  cursor: pointer;
    transition: all 0.2s ease-in-out;
    width:20px;

    &:before{
      content: "";
      display: block;
      position: absolute;
      width: 20px;
      height: 20px;
      top: 0;
      left: 0;
      background-color:#e9e9e9;
      transition: all 0.2s ease-in-out;
    }
    &:checked:before{
     content: "";
     display: block;
     position: absolute;
     width: 20px;
     height: 20px;
     top: 0;
     left: 0;
     background-color:var(--GreenStrong);
    transition: all 0.2s ease-in-out;
    }
    &:checked:after{
      content: "";
      display: block;
      width: 5px;
      height: 10px;
      border: solid white;
      border-width: 0 2px 2px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
      position: absolute;
      top: 2px;
      left: 6px;
      transition: all 0.2s ease-in-out;
    }

`

