export const SideBottombarContainer = styled.aside`
  position:fixed;
  z-index: 999;
  width:100%;
  height:${({isOpen}) => (isOpen ? '25%' : '15%')};
  border-top:3px solid var(--GreenStrong);
  display: grid;
  justify-content:center;
  align-items:center;
  grid-template-columns:1fr 1fr;
  grid-template-rows:1fr;
  grid-template-areas: "i d"; 
  overflow:scroll;

  @media screen and (max-width: 480px){
    height:${({isOpen}) => (isOpen ? '30vh' : '20vh')};
    display:flex;
    flex-direction:column;
    justify-content:flex-start;
    align-items:center;
  }

  left: 0;
  bottom:0;
  transition: 0.3s ease-in-out;

    &:before{
     content: "";
     display: block;
     position: absolute;
     width: 70px;
     height: 70px;
     top: -40px;
     left: calc(50% - 38px);
     background-color:var(--White);
     border:3px solid var(--GreenStrong);
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(45deg);
    transition: all 0.2s ease-in-out;
    z-index: 1000;


    }
    &:after{
      content: "";
      display: block;
      width: 20px;
      height: 20px;
      border: solid var(--GreenStrong);
      border-width: 0 5px 5px 0;
      -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
      transform: rotate(225deg);
      position: absolute;
      top: 10px;
      left: 49.3%;
      transition: all 0.2s ease-in-out;
    z-index: 1000;

    @media screen and (max-width: 1200px){
      left: 48.5%;
    }

      @media screen and (max-width: 480px){
        left: 47%;
      }
    }
`;

