import * as React from 'react';
import styled from 'styled-components';

export const GuestElementsContainer = styled.div`
  display:${({OpenGuestBar}) => (OpenGuestBar ? 'block' : 'none')};
  position:absolute;
  bottom:-60px;
  background:white;
  z-index:30;
  width:100%;
  height:60px;

  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
    bottom:-120px;
    height:120px;
  }
`;
export const GuestElementsWrapper = styled.div`
  display:grid;
  align-items:center;
  justify-content:center;
  grid-template-columns:1fr 1fr 0.4fr 1fr 0.4fr 1fr 0.4fr 1fr;
  grid-template-areas: "H A CA K CK B CB BL";
  height:100%;
  width:100%;
  
  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
    grid-template-columns:1fr 0.4fr 1fr 0.4fr;
    grid-template-rows:auto auto auto;
    grid-template-areas: "H H H H" "A CA K CK" "B CB BL BL"; 
    height:auto;
  }
`;

export const Blocks = styled.div`
  grid-area:${props => props.gridarea};
  width:100%;
  height:100%;
  display:flex;
  justify-content:center;
  align-items:center;
  text-align:center;
`;

export const SmallBlocks = styled.div`
  width:100%;
  height:100%;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  text-align:center;
  `;

export const ButtonIncrement = styled.button`
  width:100%;
  height:100%;
  display:flex;
  justify-content:center;
  align-items:center;
  text-align:center;
  background-color:white;
  border:none;
  color:var(--GreenStrong);
  font-size: 25px;
  font-weight:bold;
  cursor:pointer;

  &:hover{
    color:#fff;
    background-color:var(--GreenStrong);
  }

  @media screen and (max-width: 480px){
    font-size: 20px;
  }

`;
export const ButtonDecrease = styled.button`
  width:100%;
  height:100%;
  display:flex;
  justify-content:center;
  align-items:center;
  text-align:center;
  background-color:white;
  border:none;
  color:var(--GreenStrong);
  font-size: 25px;
  font-weight:bold;
  cursor:pointer;

  &:hover{
    color:#fff;
    background-color:var(--GreenStrong);
  }

  @media screen and (max-width: 480px){
    font-size: 20px;
  }
`;

export const IncrementBlock = styled.div`
  width:100%;
  height:100%;
  display:flex;
  justify-content:center;
  align-items:center;
  text-align:center;
`
export const DecrementBlock = styled.div`
  width:100%;
  height:100%;
  display:flex;
  justify-content:center;
  align-items:center;
  text-align:center;
`

export const TextLabel = styled.p`
  color:#797d83;
`
export const Number = styled.h1`
  margin-right:10px;
  color:var(--GreenStrong);
  font-size: 40px;

  @media screen and (max-width: 480px){
    font-size: 18px;
  }

`
export const SignNumber = styled.p`
  color:var(--GreenStrong);
  font-size: 25px;

  @media screen and (max-width: 480px){
    font-size: 18px;
  }
`

export const GuestBar = ({OpenGuestBar, IncrementPeople, DecrementPeople, Adults, setAdults, Kids, setKids, Babys, setBabys}) => {
  return (
    <GuestElementsContainer OpenGuestBar={OpenGuestBar}>
      <GuestElementsWrapper>
        <Blocks gridarea='H'>
          <TextLabel>Habitación</TextLabel>
        </Blocks>
        <Blocks gridarea='A'>
          <Number>{Adults}</Number>
          <TextLabel>Adultos</TextLabel>
        </Blocks>
        <SmallBlocks gridarea='CA'>
          <ButtonIncrement onClick={() => IncrementPeople(Adults, setAdults)}>+</ButtonIncrement>
          <ButtonDecrease onClick={() => DecrementPeople(Adults, setAdults)}>-</ButtonDecrease>
        </SmallBlocks>
        <Blocks gridarea='K'>
          <Number>{Kids}</Number>
          <TextLabel>Niños</TextLabel>
        </Blocks>
        <SmallBlocks gridarea='CK'>
          <ButtonIncrement onClick={() => IncrementPeople(Kids, setKids)}>+</ButtonIncrement>
          <ButtonDecrease onClick={() => DecrementPeople(Kids, setKids)}>-</ButtonDecrease>
        </SmallBlocks>
        <Blocks gridarea='B'>
          <Number>{Babys}</Number>
          <TextLabel>Bebes</TextLabel>
        </Blocks>
        <SmallBlocks gridarea='CB'>
          <ButtonIncrement onClick={() => IncrementPeople(Babys, setBabys)}>+</ButtonIncrement>
          <ButtonDecrease onClick={() => DecrementPeople(Babys, setBabys)}>-</ButtonDecrease>
        </SmallBlocks>
        <Blocks gridarea='BL'>
        </Blocks>
      </GuestElementsWrapper>
    </GuestElementsContainer >
  );
};
