import styled from 'styled-components'
import {BsCheckCircle} from 'react-icons/bs';
import {RiCloseCircleLine} from 'react-icons/ri';
import {FaRegSun} from 'react-icons/fa'
import {Link as LinkR} from 'react-router-dom'

export const SpecialistsContainer = styled.div`
  position:relative;
  height:100%;
  width:100%;
`;
export const SpecialistsWrapper = styled.div`
  height:100%;
  width:100%;
  display:grid;
  grid-template-columns:1fr;
  grid-template-rows:75vh;
  grid-template-areas:"content";

  @media screen and (max-width:480px){
    grid-template-columns:1fr;
    grid-template-rows:auto;
    grid-template-areas:"content";
  }

`;

export const Header = styled.div`
  width:100%;
  height:auto;
  text-align:center;
  font-family:var(--Header-Family);
  color:var(--White);
  font-size:3vh;
  padding:3vh;
  display:flex;
  align-items:center;
  justify-content:center;
`;

export const StepSection = styled.div`
  grid-area:step;
  height:100%;
  width:100%;
  background-color:var(--White);

`;
export const ContentWrapper = styled.div`
  grid-area:content;
  height:100%;
  width:100%;
  background:var(--White);
  background-size:cover;
  background-position:center;
  background-repeat:no-repeat;
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  overflow-y:scroll;

  @media screen and (max-width:480px){
    min-height:90vh;
    width:100%;
  }
`;

export const ThirdStepSection = styled.div`
  height:100%;
  width:100%;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;

`;

export const ContentThirdSection = styled.form`
  width:40%;
  height:60%;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
  background-color:var(--GreenLight);
  border:5px solid var(--GreenStrong);
  @media screen and (max-width:480px){
    width:80%;
  }
`

export const ButtonWrapperThirdSection = styled.div`
  width:100%;
  height:auto;
  display:flex;
  flex-direction:row;
  align-items:center;
  justify-content:space-around;
  margin:1vh 0;
  padding:1vh;
  @media screen and (max-width:480px){
    flex-direction:column;
    justify-content:center;
  }
`
export const ButtonItem = styled.div`
  @media screen and (max-width:480px){
    margin:1vh 0;
  }
`
export const SubHeaderThirdSection = styled.div`
  width:100%;
  height:auto;
  color:var(--White);
  text-align:center;
  font-size:2vh;
  padding:1vh;
`
export const CheckIcon = styled(BsCheckCircle)`
  font-size:5vh;
  margin-right:1vw;
  color:var(--White);
  @media screen and (max-width:480px){
    font-size:10vh;
    margin-right:0.5vw;
  }
`;
export const CloseIcon = styled(RiCloseCircleLine)`
  font-size:5vh;
  margin-right:1vw;
  color:var(--White);

  @media screen and (max-width:480px){
    font-size:10vh;
    margin-right:0.5vw;
  }
`;


export const GrInProgressIcon = styled(FaRegSun)`
  font-size:5vh;
  margin-right:1vw;
  color:var(--GreenStrong);

  @media screen and (max-width:480px){
    font-size:10vh;
    margin-right:0.5vw;
  }
`;

export const Button = styled(LinkR)`
  border-radius: 15px;
  text-decoration: none;
  background: ${({primary}) => (primary ? 'var(--White)' : 'var(--White)')};
  white-space:nowrap;
  padding:${({big}) => (big ? '14px 48px' : '12px 30px')};
  color:${({dark}) => (dark ? 'var(--GreenStrong)' : 'var(--GreenStrong)')};
  font-size:${({fontBig}) => (fontBig ? '20px' : '16px')};
  outline:none;
  cursor:pointer;
  display:flex;
  justify-content:center;
  align-items:center;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  transition: all 0.2s ease-in-out;

  &:hover{
    transition: all 0.2s ease-in-out;
    background: ${({primary}) => (primary ? 'var(--GreenStrong)' : 'var(--GreenStrong)')};
    color: ${({primary}) => (primary ? 'var(--White)' : 'var(--White)')};
  }

  @media screen and (max-width:480px){
    padding:${({big}) => (big ? '7px 24px' : '6px 15px')};
    font-size:${({fontBig}) => (fontBig ? '16px' : '14px')};
  }
`;

export const SubmitButton = styled.input.attrs({type: 'submit'})`
  border-radius: 15px;
  background: ${({primary}) => (primary ? '#00AAA9' : '#00AAA9')};
  white-space:nowrap;
  padding:${({big}) => (big ? '14px 48px' : '12px 30px')};
  color:${({dark}) => (dark ? 'var(--DarkBlue)' : 'var(--WhitePinkClear)')};
  font-size:${({fontBig}) => (fontBig ? '20px' : '16px')};
  outline:none;
  cursor:pointer;
  display:flex;
  justify-content:center;
  align-items:center;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  transition: all 0.2s ease-in-out;

  &:hover{
    transition: all 0.2s ease-in-out;
    background: ${({primary}) => (primary ? 'var(--DarkBlue)' : 'var(--Pink)')};
    color: ${({primary}) => (primary ? 'var(--WhitePinkClear)' : 'var(--DarkBlue)')};
  }

  @media screen and (max-width:480px){
    padding:${({big}) => (big ? '7px 24px' : '6px 15px')};
    font-size:${({fontBig}) => (fontBig ? '16px' : '14px')};
  }
`;



