import React, {useEffect, useState} from 'react'
import {ProfileContainer, ProfileWrapper, FirstColumn, SecondColumn, ImageProfileWrapper, NameProfile, UserProfileSection, SubmitButton, InputName, InputSurname, InputPhoneNumber, RedLabel, HeaderSection, DescriptionProfile} from './userProfileElements';
import NotificationBandSection from '../NotificationBand';
import ImageDefault from '../../images/profileAvatar/male_avatar.svg'
import ImageDefaultWoman from '../../images/profileAvatar/female_avatar.svg'
import axios from 'axios';
import {PATH_PREFIX_BACKEND} from "../../Data/EnvironmentVariables"
import LoadingAsync from '../LoadingAsync'



const UserProfileSectionPage = ({user, token, setUser}) => {

  const [popState, setPopState] = useState(false);
  /***********************LOGIC FOR ACTIVE RED LABELS***********************************/
  const EmptyField = (event) => {
    var id = event.target.id;
    var element = document.getElementById(id);
    var label = document.getElementById(`L${id}`);
    if (element.value === "") {
      label.classList.remove("isActive");
      label.classList.add("isActive");
    } else {
      label.classList.remove("isActive");
    }
  }

  const [consultantView, setConsultantView] = useState(false);

  useEffect(() => {
    if (user !== null) {
      var NameInput = document.getElementById('Name')
      NameInput.value = user.name;
      var phoneNumberInput = document.getElementById('PhoneNumber')
      phoneNumberInput.value = user.phonenumber;
      var AddressInput = document.getElementById('Email2')
      AddressInput.value = user.email2;
    }
  }, [user])

  /***********************LOGIC FOR SAVE USER DATA***********************************/

  const [formData, setFormData] = useState({})
  const [formDataImage, setFormDataImage] = useState({})
  const [triggerData, setTriggerData] = useState(false);
  const [notAllInformationComplete, setNotAllInformationComplete] = useState(false)


  const SubmitData = (event) => {
    event.preventDefault();
    var message = [];

    if (document.getElementById('Name').value.length === 0) {
      message.push("Ingresa correctamente tu Nombre")
    }
    if (document.getElementById('PhoneNumber').value.length === 0) {
      message.push("Ingresa correctamente tus Numero de Telefono")
    }
    if (document.getElementById('Email2').value.length === 0) {
      message.push("Ingresa correctamente tu Numero de Documento")
    }

    if (message.length !== 0) {
      setNotAllInformationComplete(true);
    } else {
      setFormData(prevFormData => ({...prevFormData, 'name': document.getElementById('Name').value}))
      setFormData(prevFormData => ({...prevFormData, 'phoneNumber': document.getElementById('PhoneNumber').value}))
      setFormData(prevFormData => ({...prevFormData, 'email2': document.getElementById('Email2').value}))
      setTriggerData(true);
    }
  }

  const [PatchAPIUser, setPatchAPIUser] = useState({tipo: 'PATCH', url: PATH_PREFIX_BACKEND + '/api/users/update/'});

  const [errorAPI, setErrorAPI] = useState(null);
  const [respuestaAPI, setRespuestaAPI] = useState(null);

  const headers = {};
  useEffect(() => {
    if (triggerData === true) {
      const PatchUserAPI = async () => {
        setErrorAPI(null);
        headers['Authorization'] = token;
        try {
          await axios({method: PatchAPIUser.tipo, url: PatchAPIUser.url, data: formData, headers: headers}).then(response => {
            setRespuestaAPI(response.data);
            setUser(response.data)
            setPopState(false)
          })
        } catch (error) {
          setErrorAPI(error.response)
          setPopState(false)
        }
        setTriggerData(false)
      };
      setPopState(true)
      PatchUserAPI();
    }
  }, [formData, triggerData])

  /*******************************NOTIFCATION VARIABLES************************************************/
  const [typeNotification, setTypeNotification] = useState("Success");
  const [MessageNotification, setMessageNotification] = useState("No Message");
  const [TriggerNotification, setTriggerNotification] = useState(false);

  const AppearNotification = async () => {
    await new Promise((r) => setTimeout(r, 4000));
    await setTriggerNotification(false);
  };

  useEffect(() => {
    if (errorAPI !== null) {
      setTypeNotification("Error");
      setMessageNotification("No se ha podido Iniciar Sesion verifica tu usuario/contraseña.");
      setTriggerNotification(true);
      AppearNotification();
      setErrorAPI(null);
    } else if (respuestaAPI !== null) {
      setTypeNotification("Success");
      setMessageNotification("Información de Perfil Actualizada.");
      setTriggerNotification(true);
      AppearNotification();
      setRespuestaAPI(null);
    } else if (notAllInformationComplete === true) {
      setTypeNotification("Warning");
      setMessageNotification("No dejar campos en blanco para actualizar Información.");
      setTriggerNotification(true);
      AppearNotification();
      setNotAllInformationComplete(false)
    }
  }, [errorAPI, respuestaAPI, notAllInformationComplete])

  return (
    <ProfileContainer>
      <LoadingAsync Open={popState} />
      <NotificationBandSection type={typeNotification} message={MessageNotification} trigger={TriggerNotification} />
      <ProfileWrapper>
        <FirstColumn>
          <ImageProfileWrapper imgsrc={ImageDefault}></ImageProfileWrapper>
          <NameProfile>{user.name}</NameProfile>
          <DescriptionProfile>En esta sección puedes editar tu información de contacto e información personal para recibir tu información a esos correos</DescriptionProfile>
        </FirstColumn>
        <SecondColumn consultantView={consultantView} onSubmit={(event) => {SubmitData(event)}}>
          <UserProfileSection>
            <HeaderSection>Información de Usuario</HeaderSection>
            <InputName id="Name" onChange={(event) => {EmptyField(event)}} />
            <RedLabel id="LName">Ingresa tu nombre</RedLabel>
            <InputSurname id="Email2" onChange={(event) => {EmptyField(event)}} />
            <RedLabel id="LEmail2">Ingresa tu Correo Electronico Secundario</RedLabel>
            <InputPhoneNumber id="PhoneNumber" onChange={(event) => {EmptyField(event)}} />
            <RedLabel id="LPhoneNumber">Ingresa tu Numero de Telefono</RedLabel>
            <SubmitButton big={false} primary={true} dark={true} value="Actualizar" />
          </UserProfileSection>
        </SecondColumn>
      </ProfileWrapper>
    </ProfileContainer >
  )
}

export default UserProfileSectionPage;
