import React, {useState, useEffect} from 'react'
import {SidebarContainer, Icon, CloseIcon, SidebarWrapper, SidebarMenu, SidebarLink, SidebarLinkScroll, SideBtnWrap} from './SideBarElements'
import {ButtonR} from '../ButtonElements';

const SidebarSection = ({isOpen, toggle, user, CloseSession}) => {

  const [activateUser, setActivateUser] = useState(false);
  useEffect(async () => {
    if (user !== null) {
      setActivateUser(true);
    } else {
      setActivateUser(false);
    }
  }, [user])

  const closeAll = () => {
    toggle();
    CloseSession();
  }

  return (
    <SidebarContainer isOpen={isOpen} onClick={toggle}>
      <Icon onClick={toggle}>
        <CloseIcon />
      </Icon>
      <SidebarWrapper>
        <SidebarMenu>
          <SidebarLink to="/" onClick={toggle}>Inicio</SidebarLink>
          <SidebarLink to="/cart-tents" onClick={toggle}>Reserva tu Glapimng</SidebarLink>
          <SidebarLink to="/cart-extras" onClick={toggle}>Agrega Adicionales a tu reserva</SidebarLink>
          {activateUser === true && (
            <>
            <SidebarLink to="/" onClick={closeAll}>Cerrar Sesión</SidebarLink>
            </>
          )}
          {activateUser === false && (
            <SidebarLink to="/register" onClick={toggle}>Registrate</SidebarLink>
          )}
        </SidebarMenu>
        <SideBtnWrap>
          {activateUser === true && (
            <ButtonR to="/userpanel" big={true} fontBig={true} onClick={toggle}>Panel de Usuario</ButtonR>
          )}
          {activateUser === false && (
            <ButtonR to="/signin" big={true} fontBig={true} onClick={toggle}>Inicia Sesión</ButtonR>
          )}
        </SideBtnWrap>
      </SidebarWrapper>
    </SidebarContainer>
  )
}

export default SidebarSection

