import React, {useState, useEffect} from 'react';
import styled, {css, keyframes} from 'styled-components/macro';
import {IoArrowForward, IoArrowBack} from 'react-icons/io5';


const HeroSection = styled.div`
  height:100%;
  height:100%;
  position:relative;
  overflow:hidden;
`;

const HeroWrapper = styled.div`
  width:100%;
  height:100%;
  display:flex;
  justify-content:center;
  align-items:center;
  overflow:hidden;
  position:relative;
`;

const HeroSlide = styled.div`
  z-index:1;
  width:100%;
  height:100%;
`;
const HeroSlider = styled.div` 
  position:absolute;
  top:0;
  left:0;
  padding:0;
  width:100%;
  height:100%;
  display:flex;
  align-items:center;
  justify-content:center;
  transition: all 0.2s ease-in-out;

  &::before{
    content:'';
    position:absolute;
    z-index:2;
    width:100%;
    height:100%;
    bottom:0vh;
    left:0;
    overflow:hidden;
    opacity:0.4;
    background:linear-gradient(
    0deg,
    rgba(0,0,0,0.2) 0%,
    rgba(0,0,0,0.2) 50%,
    rgba(0,0,0,0.6) 100%);
  }
`;

const HeroImage = styled.img`
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  object-fit:contain;
  transition: all 0.2s ease-in-out;
`;
const SliderButtons = styled.div`
  width:100%;
  display:flex;
  align-items:center;
  justify-content:space-evenly;
  flex-direction:row;
  z-index:10;
`

const arrowButtons = css`
  width:30px;
  height:30px;
  color:var(--White);
  cursor:pointer;
  border-radius:50px;
  background:var(--GreenStrong);
  padding:10px;
  margin-right:1rem;
  user-select:none;
  transition:0.3s;

  &:hover{
    background:var(--GreenLight);
    transform:scale(1.05);
  }
`
const PrevArrow = styled(IoArrowBack)`
  ${arrowButtons}
`

const NextArrow = styled(IoArrowForward)`
  ${arrowButtons}
`
export const Carousel = ({SliderData, dataImages}) => {
  const [slides, setSlides] = useState([])

  const cleanData = () => {
    var bufferlocal = []
    for (var item of Object.values(SliderData)) {
      bufferlocal.push(item.replaceAll("`", ""))
    }
    return bufferlocal
  }
  useEffect(() => {
    var buffer = cleanData();
    setSlides(buffer)
  }, [])
  return (
    <HeroSection>
      <HeroWrapper>
        {slides.map((slide, index) => {
          return (
            <HeroSlide key={index}>
              {index === dataImages.current && (
                <HeroSlider>
                  <HeroImage src={slide} alt={slide} />
                </HeroSlider>
              )}
            </HeroSlide>
          )
        })}

      </HeroWrapper>
    </HeroSection>
  );
};

export const CarouselButtons = ({nextSlide, prevSlide, dataId}) => {
  return (
    <SliderButtons>
      <PrevArrow id={dataId} onClick={(event) => {prevSlide(event)}} />
      <NextArrow id={dataId} onClick={(event) => {nextSlide(event)}} />
    </SliderButtons>
  )
}

