import styled from 'styled-components';


export const ContacUsContainer = styled.div`
  width:100%; 
  height:900px;
  background:var(--White);
  display:flex;
  box-sizing: border-box;
  padding: 120px 140px 60px 140px;
  flex-direction:column;
  justy-content:center;
  align-items:center;
  
  @media screen and (max-width: 480px){
    transition: all 0.2s ease-in-out;
    padding: 60px 0px 60px 0px;
    height:auto;
  }
`;

export const ContactUsHeader = styled.h1`
  text-align:center;
  color:var(--GreenLight);
  font-family:var(--Header-Family);
  font-size: 40px;
`;
export const ContactUsSubtitle = styled.p`
  text-align:center;
  color:var(--PinkLight);
  font-family:var(--Text-Family-Secondary);
  font-size: 18px;
`;

export const ContactUsWrapper = styled.div`
  display: grid;
  box-sizing: border-box;
  align-items: center;
  margin:20px;
  justify-content: center;
  grid-template-columns:1fr 1fr;
  grid-template-rows:500px;
  grid-template-areas: "col1 col2";
  grid-gap:20px;
  width: 100%;
  height:100%;
  transition: all 0.2s ease-in-out;
  
  @media screen and (max-width: 768px){
    margin:10px 0px;
    transition: all 0.2s ease-in-out;
    grid-gap:0px;
    grid-template-columns:1fr;
    grid-template-rows:600px;
    grid-template-areas: "col1";
  }
`;
export const Column1 = styled.div`
  width:100%;
  height:100%;
  grid-area:col1;
  background:white;
  box-sizing: border-box;
`;
export const Column2 = styled.div`
  width:100%;
  height:100%;
  grid-area:col2;
  box-sizing: border-box;
  align-items:center;
  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
    display:none;
  }
`;
export const Form = styled.form`
  display:flex;
  flex-direction:column;
  align-items:center;
  padding:50px 25px 20px 50px;
  box-sizing: border-box;
  height:100%;
  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
    padding:50px 0px 20px 0px;
  }
  @media screen and (max-width: 480px){
    transition: all 0.2s ease-in-out;
    justify-content:center;
  }
`;

export const ImgContacUs = styled.div`
  width:100%;
  height:100%;
  padding:10px;
  background-image:linear-gradient(rgba(0, 0, 0, 0.25),rgba(0, 0, 0, 0)) ,url(${props => props.imgsrc});
  background-size:cover;
  background-position:center;
`;

export const Group1 = styled.div`
  box-sizing:content;
  justify-content:center;
  height:100%;
  margin:0;
`;

export const Label = styled.label`
  color: var(--GreenLight);
  font-size: 18px;
  font-weight: normal;
  pointer-events: none;
  left: 5px;
  top: 10px;
  transition: 0.2s ease all;
`;


export const InputField = styled.input`
  font-size: 18px;
  padding: 10px 10px 10px 5px;
  display: block;
  width: 400px;
  border: none;
  border-bottom: 1px solid var(--GreenStrong);

  &:focus{
    outline: none;
  }

  @media screen and (max-width: 480px){
    width: 300px;
  }
`;
export const InputFieldText = styled.textarea`

  padding: 10px 10px 10px 5px;
  width: 400px;
  height:80px;
  display: block;
  outline:none;
  border:none;
  border-bottom: 1px solid var(--GreenStrong);
  
  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
    height:150px;
  }

  @media screen and (max-width: 480px){
    width: 300px;
  }
`;

export const SubmitButton = styled.button`
  background:var(--White);
  white-space:nowrap;
  padding:10px 3px;
  margin:10px 0;
  color:var(--GreenLight);
  font-size:20px;
  outline:none;
  border:2px solid var(--GreenLight); 
  border-radius:8px;
  cursor:pointer;
  width:400px;
  height:40px;

  display:flex;
  justify-content:center;
  align-items:center;
  text-decoration:none;
  transition: all 0.2s ease-in-out;

  &:hover{
    transition: all 0.2s ease-in-out;
    background:var(--GreenLight);
    color:var(--White);
  }

  @media screen and (max-width: 768px){
    transition: all 0.2s ease-in-out;
    padding:30px 0px; 
  }

  @media screen and (max-width: 480px){
    transition: all 0.2s ease-in-out;
    width:300px;
  }
`;

export const Iframe = styled.iframe`
  display:none;
`
