import styled, {keyframes} from 'styled-components';
import {MdError, MdWarning, } from 'react-icons/md'
import {FaCheckCircle} from 'react-icons/fa'

export const fade = keyframes`
  0%{ opacity: 0}
  25%{ opacity: 1}
  50%{ opacity: 1}
  75%{ opacity: 1}
  100%{ opacity: 0}
`

export const BandContainer = styled.aside`
  position:fixed;
  z-index: 100000;
  width:96%;
  height:5vh;
  bottom:2vh;
  margin:0 2%;
  border-radius:1vh;
  background-color:var(--PinkLight);
  display:${({activeNotification}) => (activeNotification ? 'block' : 'none')};
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  animation-duration: 4000ms;
  animation-name: ${fade};
  animation-direction: alternate;
  animation-iteration-count: 1;
`
export const BandWrapper = styled.div`
  width:100%;
  height:100%;
  display:grid;
  grid-template-columns:0.1fr 0.9fr;
  grid-template-rows:1fr;
  grid-template-areas:"icon content";
`
export const BandIcon = styled.div`
  grid-area:icon; 
  width:100%;
  height:100%;
  display:flex;
  flex-direction:rows;
  align-items:center;
  justify-content:center;
`
export const BandContent = styled.div`
  grid-area:content; 
  width:100%;
  height:100%;
  display:flex;
  flex-direction:rows;
  align-items:center;
  justify-content:flex-start;
  color:var(--White);
  font-size:2vh;
`
export const IconSuccess = styled(FaCheckCircle)`
  color:var(--GreenStrong);
  font-size:3vh;
`
export const IconWarning = styled(MdWarning)`
  color:var(--PinkStrong);
  font-size:3vh;
`
export const IconError = styled(MdError)`
  color:var(--PinkDark);
  font-size:3vh;
`

